import BigNumber from 'bignumber.js'

export const MINUTE_IN_SECONDS = 60
export const HOUR_IN_SECONDS = 3600
export const DAY_IN_SECONDS = 86400
export const WEEK_IN_SECONDS = 604800
export const DAYS = 'Day(s)'
export const HOURS = 'Hour(s)'
export const WEEKS = 'Week(s)'
/**
 * Format number of seconds into year, month, day, hour, minute, seconds
 *
 * @param seconds
 */
const getTimePeriods = (seconds) => {
  let delta = Math.abs(seconds)
  const timeLeft = {
    day: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  }
  if (delta >= DAY_IN_SECONDS) {
    timeLeft.day = Math.floor(delta / DAY_IN_SECONDS)
    delta -= timeLeft.day * DAY_IN_SECONDS
  }

  if (delta >= HOUR_IN_SECONDS) {
    timeLeft.hours = Math.floor(delta / HOUR_IN_SECONDS)
    delta -= timeLeft.hours * HOUR_IN_SECONDS
  }

  if (delta >= MINUTE_IN_SECONDS) {
    timeLeft.minutes = Math.floor(delta / MINUTE_IN_SECONDS)
    delta -= timeLeft.minutes * MINUTE_IN_SECONDS
  }

  timeLeft.seconds = delta

  return timeLeft
}

// eslint-disable-next-line consistent-return
export const convertTimeUnitToSeconds = (numberOfUnit, unit) => {
  if (unit === DAYS) {
    return new BigNumber(numberOfUnit).multipliedBy(DAY_IN_SECONDS).toString()
  }

  if (unit === HOURS) {
    return new BigNumber(numberOfUnit).multipliedBy(HOUR_IN_SECONDS).toString()
  }

  if (unit === WEEKS) {
    return new BigNumber(numberOfUnit).multipliedBy(WEEK_IN_SECONDS).toString()
  }
}

export default getTimePeriods
