import React, { useEffect, useState, useMemo } from 'react'
import { toast } from 'react-toastify'
import ReactTooltip from 'react-tooltip'
import Flex from 'components/Flex'
import useSellNft from 'hooks/useSellPackage'
import useIsApprovedNftId from 'hooks/useIsApprovedNftId'
import { useCyblocCoreContract } from 'hooks/useContract'
import useApproveTokenId from 'hooks/useApproveTokenId'
import get from 'lodash/get'
import { ReactComponent as InfoIcon } from 'assets/images/Info.svg'
import { ReactComponent as MoneyIcon } from 'assets/images/money.svg'
import { ReactComponent as TimeIcon } from 'assets/images/time.svg'
import { useSelector } from 'react-redux'
import { selectContractAddress, selectPayTokenConfig } from 'state/reducers/app'
import { SaleModalContainer } from '../BaseModal'
import FirstTab from './FirstTab'
import SecondTab from './SecondTab'
import {
  ContentContainer,
  HeaderText,
  SubText,
  Wrapper,
  OptionText,
  OptionWrapper,
  BGLeft,
  IconWrapper,
  BGRight,
} from './styles'

const SaleModal = ({ onDismiss, nftId, onRefetching = () => {} }) => {
  const [tabIndex, setTabIndex] = useState(1)
  const [isLoading, setIsLoading] = useState(false)
  const { MARKETPLACE, CYBLOC_CORE } = useSelector(selectContractAddress)
  const { onSell } = useSellNft(MARKETPLACE.ADDRESS, CYBLOC_CORE.ADDRESS)
  const { onApprove } = useApproveTokenId(MARKETPLACE.ADDRESS, CYBLOC_CORE.ADDRESS)
  const cyblocCoreContract = useCyblocCoreContract(CYBLOC_CORE.ADDRESS)
  const [isApproved, _checkIsApprovedSpendingTokenId, isLoadingApproved] = useIsApprovedNftId(
    cyblocCoreContract,
    MARKETPLACE.ADDRESS,
    nftId,
  )
  const onHandleListing = async (startingPrice, endingPrice, payToken, duration) => {
    try {
      setIsLoading(true)
      const result = await onSell(CYBLOC_CORE.ADDRESS, nftId, startingPrice, endingPrice, payToken, duration)
      setIsLoading(false)
      toast.success(`Created auction for CyBloc #${nftId}`, {
        hideProgressBar: true,
      })
      onRefetching()
      onDismiss()
    } catch (error) {
      toast.error('Fail to sell NFT')
      setIsLoading(false)
    }
  }

  const onHandleApprove = async () => {
    try {
      setIsLoading(true)
      const result = await onApprove(nftId)
      setIsLoading(false)
      toast.success(`Approved for CyBloc #${nftId}`, {
        hideProgressBar: true,
      })
    } catch (error) {
      toast.error('Fail to approve NFT')
      setIsLoading(false)
    }
  }

  const payTokenConfig = useSelector(selectPayTokenConfig)
  const payTokens = get(payTokenConfig, 'payTokens', [])
  const marketFee = get(payTokenConfig, 'marketFee', 0)
  return (
    <SaleModalContainer>
      <ContentContainer>
        <HeaderText>List CyBloc #{nftId} for sale</HeaderText>
        <Wrapper>
          <Flex alignItems="center" mb="12px">
            <SubText mr="5px">Listing Type</SubText>
            <InfoIcon data-tip data-for="listingType" />
            <ReactTooltip id="listingType">
              {tabIndex === 1 ? (
                <span>Your asset will be listed on the Marketplace with a fixed price</span>
              ) : (
                <span>
                  For auctions, you can set the price to increase or decrease over time. <br />
                  Once it reaches the final price, it&apos;ll stay on the market at this price
                </span>
              )}
            </ReactTooltip>
          </Flex>
          <OptionWrapper mb="24px">
            <BGLeft isActive={tabIndex === 1} onClick={() => setTabIndex(1)}>
              <IconWrapper mr="5px">
                <MoneyIcon />
              </IconWrapper>
              <OptionText>Fixed price</OptionText>
            </BGLeft>
            <BGRight isActive={tabIndex === 2} onClick={() => setTabIndex(2)}>
              <IconWrapper mr="5px">
                <TimeIcon />
              </IconWrapper>
              <OptionText>Time auction</OptionText>
            </BGRight>
          </OptionWrapper>
          {tabIndex === 1 ? (
            <FirstTab
              payTokens={payTokens}
              isApproved={isApproved}
              onHandleListing={onHandleListing}
              onHandleApprove={onHandleApprove}
              isLoadingApproved={isLoadingApproved}
              isLoading={isLoading}
              marketFee={marketFee}
            />
          ) : (
            <SecondTab
              payTokens={payTokens}
              isApproved={isApproved}
              onHandleListing={onHandleListing}
              onHandleApprove={onHandleApprove}
              isLoadingApproved={isLoadingApproved}
              isLoading={isLoading}
              marketFee={marketFee}
            />
          )}
        </Wrapper>
      </ContentContainer>
    </SaleModalContainer>
  )
}

export default SaleModal
