import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { approveNftId } from 'utils/callHelpers'
import { useCyblocCoreContract, useCyblocMarketContract } from './useContract'

const useApproveTokenId = (marketContractAddress, cyblocCoreAddress) => {
  const { account } = useWeb3React()
  const cyblocMarketContract = useCyblocMarketContract(marketContractAddress)
  const cyblocCoreContract = useCyblocCoreContract(cyblocCoreAddress)
  const handleApproveTokenId = useCallback(
    async (tokenId) => {
      const tx = await approveNftId(marketContractAddress, tokenId, cyblocCoreContract, account)

      return tx
    },
    [account, cyblocMarketContract, cyblocCoreContract],
  )

  return { onApprove: handleApproveTokenId }
}

export default useApproveTokenId
