import React from 'react'
import styled from 'styled-components'
import { toast } from 'react-toastify'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Box from 'components/Box'

const Wrapper = styled(Box)`
  position: relative;
  // margin: 06px 0px 50px 0px;
  width: 100%;
  min-height: 26px;
`
const Bg = styled(Box)`
  height: 100%;
  width: 100%;
  opacity: 0.6;

  position: absolute;
  clip-path: polygon(
    149px 0,
    calc(100% - 63px) 0,
    calc(100% - 25px) 23px,
    100% 23px,
    100% calc(100% - ${(props) => (props.isOpen ? '10px' : '0px')}),
    calc(100% - 25px) 100%,
    25px 100%,
    0 calc(100% - ${(props) => (props.isOpen ? '10px' : '0px')}),
    0 23px,
    127px 23px
  );
  background: linear-gradient(180deg, #b596dc 0, #3d296a 150px);
  &::after {
    content: '';
    display: block;
    position: relative;
    top: 2px;
    left: 2px;
    height: calc(100% - 4px);
    width: calc(100% - 4px);
    clip-path: polygon(
      147.9px 0,
      calc(100% - 62px) 0,
      calc(100% - 23.5px) 23px,
      100% 23px,
      100% calc(100% - ${(props) => (props.isOpen ? '10px' : '0px')}),
      calc(100% - 25px) 100%,
      25px 100%,
      0 calc(100% - ${(props) => (props.isOpen ? '10px' : '0px')}),
      0 23px,
      125.9px 23px
    );

    background: linear-gradient(154.37deg, rgba(85, 95, 191, 0.5) 9.75%, rgba(69, 30, 67, 0.5) 95.05%), #12106b;
  }
`

const StyleHeader = styled(Text)`
  position: absolute;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
  /* or 24px */
  letter-spacing: 0.2em;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fdf9eb;
  margin-top: -3px;
  margin-left: 8px;
  &:hover {
    cursor: pointer;
  }
`
const DropdownIcon = styled.img`
  position: absolute;
  top: 5px;
  transform: ${(props) => (props.isOpen ? 'rotate(180deg)' : 'rotate(0deg)')};
  transition: all 0.3s ease-out;
  right: 5px;
  &:hover {
    cursor: pointer;
  }
`

const DropDownFilter = ({ mt = '0px', isOpen, title, children, onChangeState }) => {
  return (
    <Wrapper mt={mt}>
      <Bg isOpen={isOpen} />
      <StyleHeader onClick={onChangeState}>{title}</StyleHeader>
      {/* <Box onClick = {onChangeState}>Open</Box> */}
      {children}
      <DropdownIcon src="/images/dropdown-icon.png" isOpen={isOpen} onClick={onChangeState} />
    </Wrapper>
  )
}
export default DropDownFilter
