import React, { useContext, useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Collapse } from 'react-collapse'
import styled from 'styled-components'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import LoaderIcon from 'components/LoaderIcon'
import { selectNftTokens, selectSkillFilter, setSkillFilter } from 'pages/Marketplace/store/reducer'
import { MarketplaceContext } from 'pages/Marketplace/context/MarketplaceContext'
import DropDownFilter from '../DropDownFilter'
import SkillSlider from './utils/SkillSlider'

const Content = styled(Flex)`
  padding: 45px 24px 10px 24px;
  flex-direction: column;
  transition: height 500ms;
`
const Test = styled(Flex)`
  width: 200px;
  height: 100px;
`
export const Button = styled(Box)`
  display: flex;
  justify-content: center;
  margin: 10px 0px;
  align-items: flex-start;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 280px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 15px 10px;
  z-index: 999;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:hover {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    font-weight: 700;
  }
`
export const BaseText = styled(Text)`
  font-weight: 500;
  color: #ffffff;
`

const SkillFilter = ({ setQuery }) => {
  const [isOpen, setIsOpen] = useState(true)
  const { state, marketplaceDispatch } = useContext(MarketplaceContext)
  const skillFilter = useSelector(() => selectSkillFilter(state))
  const { shooting, crossing, tackling, dribbling, physical, passing } = skillFilter
  const [skillsFilter, setSkillsFilter] = useState({
    shootingFilter: shooting,
    crossingFilter: crossing,
    tacklingFilter: tackling,
    dribblingFilter: dribbling,
    physicalFilter: physical,
    passingFilter: passing,
  })

  const onChangeState = () => {
    setIsOpen(!isOpen)
  }

  const { shootingFilter, crossingFilter, tacklingFilter, dribblingFilter, physicalFilter, passingFilter } =
    skillsFilter

  const onHandleFilterSkill = () => {
    marketplaceDispatch(
      setSkillFilter({
        ...skillFilter,
        shooting: shootingFilter,
        crossing: crossingFilter,
        tackling: tacklingFilter,
        dribbling: dribblingFilter,
        physical: physicalFilter,
        passing: passingFilter,
      }),
    )
    setQuery({
      shootingMin: shootingFilter.min,
      shootingMax: shootingFilter.max,
      crossingMin: crossingFilter.min,
      crossingMax: crossingFilter.max,
      tacklingMin: tacklingFilter.min,
      tacklingMax: tacklingFilter.max,
      dribblingMin: dribblingFilter.min,
      dribblingMax: dribblingFilter.max,
      physicalMin: physicalFilter.min,
      physicalMax: physicalFilter.max,
      passingMin: passingFilter.min,
      passingMax: passingFilter.max,
    })
  }

  return (
    <DropDownFilter mt="30px" isOpen={isOpen} title="Skill" onChangeState={onChangeState}>
      <Collapse isOpened={isOpen}>
        <Content>
          <SkillSlider
            title="Shooting"
            rangeValue={shootingFilter}
            handleOnChange={(value) => {
              setSkillsFilter({
                ...skillsFilter,
                shootingFilter: value,
              })
            }}
          />
          <SkillSlider
            title="Crossing"
            rangeValue={crossingFilter}
            handleOnChange={(value) => {
              setSkillsFilter({
                ...skillsFilter,
                crossingFilter: value,
              })
            }}
          />
          <SkillSlider
            title="Tackling"
            rangeValue={tacklingFilter}
            handleOnChange={(value) => {
              setSkillsFilter({
                ...skillsFilter,
                tacklingFilter: value,
              })
            }}
          />
          <SkillSlider
            title="Passing"
            rangeValue={passingFilter}
            handleOnChange={(value) => {
              setSkillsFilter({
                ...skillsFilter,
                passingFilter: value,
              })
            }}
          />
          <SkillSlider
            rangeValue={dribblingFilter}
            title="Dribbling"
            handleOnChange={(value) => {
              setSkillsFilter({
                ...skillsFilter,
                dribblingFilter: value,
              })
            }}
          />
          <SkillSlider
            rangeValue={physicalFilter}
            title="Physical"
            handleOnChange={(value) => {
              setSkillsFilter({
                ...skillsFilter,
                physicalFilter: value,
              })
            }}
          />
          <Flex justifyContent="center">
            <Button onClick={onHandleFilterSkill}>
              {false ? <LoaderIcon /> : <BaseText fontSize="2rem">APPLY</BaseText>}
            </Button>
          </Flex>
        </Content>
      </Collapse>
    </DropDownFilter>
  )
}
export default SkillFilter
