import React, { useCallback, useEffect, useState, useRef } from 'react'
import './multiRangeSlider.css'

const MultiRangeSlider = ({ limit_range, value_range, onChange }) => {
  const [minVal, setMinVal] = useState(value_range.min)
  const [maxVal, setMaxVal] = useState(value_range.max)
  const minValRef = useRef(value_range.min)
  const maxValRef = useRef(value_range.max)
  const range = useRef(null)

  // Convert to percentage
  const getPercent = useCallback(
    (value) => Math.round(((value - limit_range.min) / (limit_range.max - limit_range.min)) * 100),
    [limit_range],
  )

  // Set width of the range to decrease from the left side
  useEffect(() => {
    const minPercent = getPercent(minVal)
    const maxPercent = getPercent(maxValRef.current)

    if (range.current) {
      range.current.style.left = `${minPercent}%`
      range.current.style.width = `${maxPercent - minPercent}%`
    }
  }, [minVal, getPercent])

  // Set width of the range to decrease from the right side
  useEffect(() => {
    const minPercent = getPercent(minValRef.current)
    const maxPercent = getPercent(maxVal)

    if (range.current) {
      range.current.style.width = `${maxPercent - minPercent}%`
    }
  }, [maxVal, getPercent])

  // Get min and max values when their state changes
  useEffect(() => {
    onChange({ min: minVal, max: maxVal })
  }, [minVal, maxVal, onChange])

  return (
    <div className="container">
      <input
        type="range"
        min={limit_range.min}
        max={limit_range.max}
        value={minVal}
        onChange={(event) => {
          const value = Math.min(Number(event.target.value), maxVal - 1)
          setMinVal(value)
          minValRef.current = value
        }}
        className="thumb thumb--left"
        style={{ zIndex: minVal > limit_range.max - 100 && '5' }}
      />
      <input
        type="range"
        min={limit_range.min}
        max={limit_range.max}
        value={maxVal}
        onChange={(event) => {
          const value = Math.max(Number(event.target.value), minVal + 1)
          setMaxVal(value)
          maxValRef.current = value
        }}
        className="thumb thumb--right"
      />

      <div className="slider">
        <div className="slider__track" />
        <div ref={range} className="slider__range" />
      </div>
    </div>
  )
}

export default MultiRangeSlider
