import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { useAppDispatch } from 'state'
import { API_MARKETPLACE } from 'config/constant'
import { setSaleConfig } from 'state/reducers/app'

const useGetSaleConfig = () => {
  const dispatch = useAppDispatch()
  useEffect(() => {
    const getFilterConfig = async () => {
      try {
        const calls = []
        const apiList = [
          `${API_MARKETPLACE}/master/cybloc/traits`,
          `${API_MARKETPLACE}/master/cybloc/types`,
          `${API_MARKETPLACE}/master/market`,
        ]

        apiList.forEach((apiLink) => {
          calls.push(axios.get(apiLink))
        })

        const result = await Promise.all(calls)
        if (result && result.length !== 0) {
          const mappedData = result.map((r) => r.data)
          dispatch(setSaleConfig(mappedData))
        }

        return null
      } catch (error) {
        return null
      }
    }

    getFilterConfig()
  }, [])

  return null
}

export default useGetSaleConfig
