const Label = styled.label`
  position: relative;
  display: inline-block;
  width: 32px;
  height: 32px;
  cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
`

const Indicator = styled.div`
  width: 32px;
  height: 32px;
  position: absolute;
  top: 0;
  left: 0;
  background: url(images/RadioButtonBg.png) no-repeat right top;
  background-size: 32px 34px;

  ${Label}:hover & {
    // background: #ccc;
  }

  &::after {
    content: '';
    position: absolute;
    display: none;
  }

  ${Input}:checked + &::after {
    display: block;
    width: 100%;
    height: 100%;

    background-color: #263238;
    background: url(images/RadioButtonBgChecked.png) no-repeat right top;
    background-size: 32px 34px;
    // width: 20px;
  }

  ${Input}:disabled + & {
    pointer-events: none;
    opacity: 0.6;
    background: #e6e6e6;
  }
`

export default function RadioButton({ value, onChange, name, id, label, disabled, checked }) {
  return (
    <>
      <Label htmlFor={id} disabled={disabled}>
        {/* <Input
          id={id}
          type="radio"
          role="radio"
          name={name}
          value={value}
          disabled={disabled}
          onChange={onChange}
          checked={checked}
        /> */}
        <Input
          id={id}
          type="checkbox"
          name={name}
          value={value}
          disabled={disabled}
          checked={checked}
          onChange={onChange}
        />
        <Indicator />
      </Label>

      {label}
    </>
  )
}
