import cyblocPresaleIdoAbi from 'config/abi/CyblocPresale.json'
import cyblocNftContractAbi from 'config/abi/CyblocPack.json'
import cyblocUtilContractAbi from 'config/abi/CyblocUtil.json'
import cyblocMarketContractAbi from 'config/abi/CyblocMarket.json'
import cyblocCoreContractAbi from 'config/abi/CyblocCore.json'
import bep20Abi from 'config/abi/erc20.json'
import { getWeb3NoAccount } from './web3'
/**
 * Get contract object
 * @param {*} abi Abi of the contract
 * @param {*} address Address of the contract
 * @param {*} web3 Web3 instance
 * @returns Contract object
 */
const getContract = (abi, address, web3) => {
  const _web3 = web3 || getWeb3NoAccount()
  return new _web3.eth.Contract(abi, address)
}

export const getBep20Contract = (address, web3) => {
  if (address) {
    return getContract(bep20Abi, address, web3)
  }

  return null
}

export const getCyblocPresaleContract = (web3, address) => {
  return getContract(cyblocPresaleIdoAbi, address, web3)
}

export const getCyblocNftContract = (web3, address) => {
  return getContract(cyblocNftContractAbi, address, web3)
}

export const getCyblocUtilContract = (web3, address) => {
  return getContract(cyblocUtilContractAbi, address, web3)
}

export const getCyblocMarketContract = (web3, address) => {
  return getContract(cyblocMarketContractAbi, address, web3)
}

export const getCyblocCoreContract = (web3, address) => {
  return getContract(cyblocCoreContractAbi, address, web3)
}
