import React, { useState, useRef, useContext } from 'react'
import { useSelector } from 'react-redux'
import Box from 'components/Box'
import Text from 'components/Text'
import Flex from 'components/Flex'
import styled from 'styled-components'
import useClickOutside from 'hooks/useClickOutside'
import { SortFilterOption } from 'config/constant'
import { selectSortFilter } from 'pages/Marketplace/store/reducer'
import { MarketplaceContext } from 'pages/Marketplace/context/MarketplaceContext'

const Wrapper = styled(Flex)`
  margin: 5px 0px;
  position: relative;
  width: 260px;
  height: 39px;
  // filter: drop-shadow(0px 1.75487px 3.50975px rgba(0, 0, 0, 0.15));
`
const BG = styled(Box)`
  position: absolute;
  height: 100%;
  width: 100%;
  clip-path: polygon(9px 0, 100% 0, 100% calc(100% - 20px), calc(100% - 15px) 100%, 0% 100%, 0% 19px);
  background: linear-gradient(180deg, #b596dc 0%, #3d296a 100%);
`
const BGLable = styled(Flex)`
  justify-content: center;
  position: absolute;
  top: 2px;
  left: 2px;
  width: 84px;
  height: 35px;
  clip-path: polygon(8px 0, 100% 0, calc(100% - 15px) 100%, 0% 100%, 0% 17.5px);
  background: #22144a;
`
const DropdownButton = styled(Flex)`
  justify-content: center;
  position: absolute;
  top: 2px;
  right: 2px;
  width: calc(100% - 76px);
  height: 35px;
  clip-path: polygon(14px 0, 100% 0, 100% calc(100% - 19px), calc(100% - 14px) 100%, 0% 100%);
  background: #513b8e;
  transition: all 0.3s ease-out;
  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
  }

  &:active {
    opacity: ${(props) => (props.disabled ? '0.6' : '1')};
  }
`
const StyleText = styled(Text)`
  margin: auto 0px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.3rem;
  color: #e0e0e0;
`
const Collapse = styled(Box)`
  position: absolute;
  top: 39px;
  width: 97%;
  z-index: 999;
  display: ${(props) => (props.isOpened ? 'block' : 'none')};
`
const DropdownOptionWrapper = styled(Box)`
  background-color: #22144a;
  transition: all 0.3s ease-out;
`
const DropdownOption = styled(Text)`
  padding: 10px 0px 10px 15px;
  font-weight: 500;
  color: #ffffff;
  align-items: center;
  z-index: 999;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 15.76px;
  transition: all 0.3s ease-out;
  display: flex;

  &:hover {
    cursor: pointer;
    background-color: #4c3886;
  }
`
const IconWrapper = styled(Box)`
  z-index: 999;
  margin: auto 0px;
`

const DropdownIcon = styled.img`
  transform: ${(props) => (props.isOpen ? 'rotate(180deg)' : 'rotate(0deg)')};
  transition: all 0.3s ease-out;
  margin-left: 5px;
  object-fit: cover;
  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
  }
`

const SortFilter = ({ setQuery }) => {
  const { state } = useContext(MarketplaceContext)
  const sortFilter = useSelector(() => selectSortFilter(state))
  const [isOpen, setIsOpen] = useState(false)
  const optionsRef = useRef(null)

  useClickOutside(optionsRef, () => {
    setIsOpen(false)
  })

  const onDismissDropdown = () => {
    setIsOpen(false)
  }

  const onHandleChange = (option) => {
    // marketplaceDispatch(setSortFilterOption(option._Id))
    setQuery({
      sort: option._Id,
      pageNumber: 1,
    })
    setIsOpen(false)
  }

  return (
    <Wrapper>
      <BG />
      <BGLable>
        <StyleText>Sort</StyleText>
      </BGLable>
      <DropdownButton
        onClick={() => {
          setIsOpen(!isOpen)
        }}
      >
        <StyleText>{SortFilterOption[sortFilter - 1].name}</StyleText>
        <IconWrapper>
          <DropdownIcon src="/images/dropdown-icon.png" isOpen={isOpen} disabled={false} />
        </IconWrapper>
      </DropdownButton>
      <Collapse isOpened={isOpen}>
        <DropdownOptionWrapper>
          {SortFilterOption.map((option) => {
            return (
              <DropdownOption
                key={option.name}
                onClick={() => {
                  onHandleChange(option)
                }}
              >
                <Text>{option.name}</Text>
              </DropdownOption>
            )
          })}
        </DropdownOptionWrapper>
      </Collapse>
    </Wrapper>
  )
}
export default SortFilter
