import { useState, useRef } from 'react'
import Box from 'components/Box'
import SearchInput from 'components/SearchInput'
import styled from 'styled-components'
import useClickOutside from 'hooks/useOnClickOutside'

const Wrapper = styled(Box)`
  position: relative;
`

const SuggestionsList = styled.ul`
  z-index: 1000;
  list-style-type: none;
  position: absolute;
  width: 90%;
  top: 39px;
  background-color: #22144a;
  max-height: 230px;
  overflow-y: scroll;
`
const SuggestionItem = styled.li`
  color: #e0e0e0;
  font-size: 1.2rem;
  padding: 10px 0px 10px 15px;
  transition: 0.3s all ease-out;
  font-weight: 600;
  background-color: ${(props) => props.isActive && '#4c3886'};
  &:hover {
    background-color: #4c3886;
    cursor: pointer;
  }

  &:not(:last-of-type) {
    margin-bottom: 10px;
  }
`

const AutoComplete = ({ suggestions, value, onChangeValue, onSearch }) => {
  const [filteredSuggestions, setFilteredSuggestions] = useState([])
  const [activeSuggestionIndex, setActiveSuggestionIndex] = useState(0)
  const suggestionRef = useRef(null)
  const [showSuggestions, setShowSuggestions] = useState(false)

  useClickOutside(suggestionRef, () => {
    setShowSuggestions(false)
  })

  const onChange = (e) => {
    const userInput = e.target.value

    // Filter our suggestions that don't contain the user's input
    const unLinked = suggestions.filter((suggestion) => suggestion.toLowerCase().indexOf(userInput.toLowerCase()) > -1)

    onChangeValue(e.target.value)
    setFilteredSuggestions(unLinked)
    setActiveSuggestionIndex(0)
    setShowSuggestions(true)
  }

  const onClick = (e) => {
    setFilteredSuggestions([])
    onChangeValue(e.target.innerText)
    setActiveSuggestionIndex(0)
    setShowSuggestions(false)
  }

  const onKeyDown = (e) => {
    // User pressed the enter key
    if (e.keyCode === 13) {
      if (value) {
        onSearch()
      }

      if (!value) {
        return
      }
      onChangeValue(filteredSuggestions[activeSuggestionIndex])
      setActiveSuggestionIndex(activeSuggestionIndex)
      setShowSuggestions(false)
    }
    // User pressed the up arrow
    else if (e.keyCode === 38) {
      if (activeSuggestionIndex === 0) {
        return
      }

      setActiveSuggestionIndex(activeSuggestionIndex - 1)
    }
    // User pressed the down arrow
    else if (e.keyCode === 40) {
      if (activeSuggestionIndex - 1 === filteredSuggestions.length) {
        return
      }

      setActiveSuggestionIndex(activeSuggestionIndex + 1)
    }
  }

  const SuggestionsListComponent = () => {
    return filteredSuggestions.length ? (
      <SuggestionsList ref={suggestionRef}>
        {filteredSuggestions.map((suggestion, index) => {
          return (
            // eslint-disable-next-line jsx-a11y/no-noninteractive-element-interactions
            <SuggestionItem key={suggestion} onClick={onClick} isActive={index === activeSuggestionIndex}>
              {suggestion}
            </SuggestionItem>
          )
        })}
      </SuggestionsList>
    ) : null
  }

  const onHandleClickSearch = () => {
    onSearch()
  }

  return (
    <Wrapper>
      <SearchInput
        onHandleClickSearch={onHandleClickSearch}
        mb="12px"
        onChange={onChange}
        onKeyDown={onKeyDown}
        value={value}
        style={{
          width: '90%',
        }}
      />
      {showSuggestions && value && <SuggestionsListComponent />}
    </Wrapper>
  )
}

export default AutoComplete
