import React, { useContext, useCallback } from 'react'
import Text from 'components/Text'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import Flex from 'components/Flex'
import { MarketplaceContext } from 'pages/Marketplace/context/MarketplaceContext'
import { selectFilters, selectType, setFilters } from 'pages/Marketplace/store/reducer'
import TabButton, { TAB_BUTTON_TYPE } from './TabButton'

const Wrapper = styled(Flex)`
  width: 100%;
  height: 39px;
`

const Title = styled(Text)`
  margin: auto 17px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 23px;
  /* identical to box height */
  align-items: center;
  color: #e0e0e0;
  z-index: 99;
`
const Tabs = () => {
  const { state, marketplaceDispatch } = useContext(MarketplaceContext)
  const nftType = useSelector(() => selectType(state))
  const filters = useSelector(() => selectFilters(state))
  const onHandleSelectType = useCallback(
    (newNftType) => {
      marketplaceDispatch(
        setFilters({
          ...filters,
          type: newNftType,
        }),
      )
    },
    [marketplaceDispatch],
  )

  return (
    <Wrapper>
      <TabButton
        type={TAB_BUTTON_TYPE.left}
        isActive={nftType === TAB_BUTTON_TYPE.left}
        onClick={() => onHandleSelectType(TAB_BUTTON_TYPE.left)}
      >
        <Title>Cyblocs</Title>
      </TabButton>
      <TabButton
        type={TAB_BUTTON_TYPE.center}
        isActive={nftType === TAB_BUTTON_TYPE.center}
        onClick={() => onHandleSelectType(TAB_BUTTON_TYPE.center)}
      >
        <Title>Eggs</Title>
      </TabButton>
      <TabButton
        type={TAB_BUTTON_TYPE.right}
        isActive={nftType === TAB_BUTTON_TYPE.right}
        onClick={() => onHandleSelectType(TAB_BUTTON_TYPE.right)}
      >
        <Title>Parts</Title>
      </TabButton>
    </Wrapper>
  )
}

export default Tabs
