import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { canceledSellNft } from 'utils/callHelpers'
import { useCyblocMarketContract } from './useContract'

const useCanceledSellNft = (marketContractAddress) => {
  const { account } = useWeb3React()
  const cyblocMarketContract = useCyblocMarketContract(marketContractAddress)
  const handleCanceledSellNft = useCallback(
    async (nftAddress, tokenId) => {
      const tx = await canceledSellNft(account, cyblocMarketContract, nftAddress, tokenId)
      return tx
    },
    [account, cyblocMarketContract],
  )

  return { onCanceledSell: handleCanceledSellNft }
}

export default useCanceledSellNft
