import { useCallback } from 'react'
import axios from 'axios'
import { useWeb3React } from '@web3-react/core'
import get from 'lodash/get'
import { API_CYBALL, CAMPAIGN_TYPE } from 'config/constant'
import { canUnpack } from 'utils/callHelpers'
import { useCyblocUtilContract } from './useContract'

// We will select correct contract, based on selected package
const useCanUnpack = (utilContractAddress, cyblocPackAddress) => {
  const { account } = useWeb3React()
  const cyblocUtilContract = useCyblocUtilContract(utilContractAddress)
  const handleCanUnpack = useCallback(
    async (packages) => {
      const tokenIdList = packages.map((p) => p.tokenId)
      const isAbleToUnpack = await canUnpack(cyblocUtilContract, tokenIdList, cyblocPackAddress)
      return isAbleToUnpack
    },
    [account, cyblocUtilContract, cyblocPackAddress],
  )

  return { canUnpack: handleCanUnpack }
}

export default useCanUnpack
