import React, { useEffect, useContext, useMemo, useState } from 'react'
import { useHistory, useRouteMatch, useLocation } from 'react-router-dom'
import { useQueryParams, StringParam, NumberParam, ArrayParam, BooleanParam, withDefault } from 'use-query-params'
import { useSelector } from 'react-redux'
import get from 'lodash/get'
import { useWeb3React } from '@web3-react/core'
import styled from 'styled-components'
import Box from 'components/Box'
import Pagination from 'components/Pagination'
import Flex from 'components/Flex'
import PageLoading from 'components/PageLoading'
import useInterval from 'hooks/useInterval'
import { selectIsLoadingConfig, selectTypeConfig } from 'state/reducers/app'
import { MarketplaceContext } from './context/MarketplaceContext'
import {
  selectNftTokens,
  setFetchingSuccess,
  setFetching,
  selectCurrentPageNumber,
  selectTotalItems,
  selectPageSzie,
  setPageNumber,
  setFilters,
  TRAIT_TYPE,
} from './store/reducer'
import { useApi } from './api'
import LeftFilter from './components/LeftFilter'
import NFTsGrid from '../../components/NFTsGrid'
import ExploreFilterHeader from './components/ExploreFilterHeader'

const Wrapper = styled(Box)`
  min-height: inherit;
`
const Container = styled(Flex)`
  position: relative;
  max-width: 1400px;
  margin: 0 auto;
  margin-top: 30px;

  @media (max-width: 1000px) {
    max-width: 90%;
  }
  @media (max-width: 576px) {
    max-width: 100%;
  }
`

const Body = styled(Box)`
  position: relative;
  width: 100%;
  align-items: flex-start;
`

const ExploreAll = styled(Box)`
  overflow-y: visible;
  padding: 5px;
`
const Marketplace = () => {
  const { account } = useWeb3React()
  const { getTokensNft } = useApi()
  const history = useHistory()
  const location = useLocation()
  const { path } = useRouteMatch()
  const { state, marketplaceDispatch } = useContext(MarketplaceContext)
  const { filters } = state
  const currentPageNumber = useSelector(() => selectCurrentPageNumber(state))
  const totalItems = useSelector(() => selectTotalItems(state))
  const pageSize = useSelector(() => selectPageSzie(state))
  const types = useSelector(selectTypeConfig)
  const { tokens, fetching } = useSelector(() => selectNftTokens(state))
  const isLoadingConfig = useSelector(selectIsLoadingConfig)
  const [isLoadingFirstTime, setIsLoadingFirstTime] = useState(true)
  const [reloadTimes, setReloadTimes] = useState(0)
  const [query, setQuery] = useQueryParams({
    pageNumber: NumberParam,
    showMyAuction: BooleanParam,
    cyblocId: NumberParam,
    classType: withDefault(ArrayParam, []),
    shootingMin: NumberParam,
    shootingMax: NumberParam,
    crossingMin: NumberParam,
    crossingMax: NumberParam,
    tacklingMin: NumberParam,
    tacklingMax: NumberParam,
    passingMin: NumberParam,
    passingMax: NumberParam,
    dribblingMin: NumberParam,
    dribblingMax: NumberParam,
    physicalMin: NumberParam,
    physicalMax: NumberParam,
    traitType: withDefault(ArrayParam, []),
    traitId: withDefault(ArrayParam, []),
    sort: NumberParam,
    payToken: StringParam,
  })

  const CLASS_TYPES = useMemo(() => {
    const typesConfig = get(types, 'cyblocTypes', [])
    if (typesConfig && typesConfig.length !== 0) {
      return typesConfig
    }
    return []
  }, [types])

  const fetchNFTs = async (_filters) => {
    marketplaceDispatch(setFetching(true))
    const data = await getTokensNft(_filters, account)
    if (data) {
      marketplaceDispatch(setFetchingSuccess(data))
    }
  }

  useEffect(() => {
    const {
      showMyAuction,
      pageNumber,
      cyblocId,
      classType,
      shootingMax,
      shootingMin,
      crossingMin,
      crossingMax,
      tacklingMax,
      tacklingMin,
      passingMax,
      passingMin,
      dribblingMax,
      dribblingMin,
      physicalMax,
      physicalMin,
      traitType,
      traitId,
      sort,
      payToken,
    } = query
    const newFilters = {
      ...filters,
    }
    if (typeof showMyAuction === 'boolean') {
      newFilters.showMyAuction = showMyAuction
    }

    if (cyblocId) {
      newFilters.id = cyblocId
    }

    if (pageNumber) {
      newFilters.page = {
        ...newFilters.page,
        curPage: pageNumber - 1,
      }
    }

    if (classType && classType.length !== 0) {
      const formattedClassType = classType.map((t) => Number(t))
      const selectedClass = CLASS_TYPES.filter((classItem) => formattedClassType.includes(classItem.id))
      newFilters.class = selectedClass
    }

    if (
      Number.isInteger(shootingMax) &&
      Number.isInteger(shootingMin) &&
      Number.isInteger(crossingMax) &&
      Number.isInteger(crossingMin) &&
      Number.isInteger(tacklingMax) &&
      Number.isInteger(tacklingMin) &&
      Number.isInteger(passingMax) &&
      Number.isInteger(passingMin) &&
      Number.isInteger(dribblingMax) &&
      Number.isInteger(dribblingMin) &&
      Number.isInteger(physicalMax) &&
      Number.isInteger(physicalMin)
    ) {
      newFilters.skill = {
        shooting: {
          min: shootingMin,
          max: shootingMax,
        },
        crossing: {
          min: crossingMin,
          max: crossingMax,
        },
        tackling: {
          min: tacklingMin,
          max: tacklingMax,
        },
        passing: {
          min: passingMin,
          max: passingMax,
        },
        dribbling: {
          min: dribblingMin,
          max: dribblingMax,
        },
        physical: {
          min: physicalMin,
          max: physicalMax,
        },
      }
    }
    if (traitType && traitType.length !== 0) {
      newFilters.trait = {
        type: TRAIT_TYPE.NAME,
        traitFilteredList: traitType,
      }
    }

    if (traitId && traitId.length !== 0) {
      newFilters.trait = {
        type: TRAIT_TYPE.ID,
        traitFilteredList: traitId,
      }
    }

    if (sort) {
      newFilters.sortFilterOption = sort
    }
    if (payToken) {
      newFilters.payToken = payToken
    }

    marketplaceDispatch(setFilters(newFilters))
    fetchNFTs(newFilters)
    setIsLoadingFirstTime(false)
  }, [location.search, reloadTimes])

  useInterval(() => {
    setReloadTimes(reloadTimes + 1)
  }, 120000)

  const onHandleChangePageNumber = (newPageNumber) => {
    setQuery({
      pageNumber: newPageNumber,
    })
  }

  const onNavigateToOpen = (item) => {
    history.push(`${path}/${item.cyblocId}`)
  }

  return (
    <Wrapper>
      {isLoadingConfig || isLoadingFirstTime ? (
        <PageLoading />
      ) : (
        <Container>
          <LeftFilter setQuery={setQuery} query={query} />
          <Body ml="34px">
            <ExploreFilterHeader setQuery={setQuery} />
            <ExploreAll mt="16px" mb="60px">
              <NFTsGrid
                items={tokens}
                loading={fetching}
                numPerRow={null}
                category={filters}
                onClickItem={onNavigateToOpen}
              />
            </ExploreAll>
            {!fetching && (
              <Pagination
                currentPage={currentPageNumber + 1}
                totalCount={totalItems}
                pageSize={pageSize}
                onPageChange={(page) => onHandleChangePageNumber(page)}
              />
            )}
          </Body>
        </Container>
      )}
    </Wrapper>
  )
}

export default Marketplace
