import React, { useMemo } from 'react'
import styled, { keyframes } from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import { selectSelectedNftId } from 'state/reducers/cybloc'
import { useSelector } from 'react-redux'
import get from 'lodash/get'
import find from 'lodash/find'
import Flex from 'components/Flex'
import Text from 'components/Text'
import Box from 'components/Box'
import CyBlocCard from 'components/CyBlocCard'
import { SaleModal, useModal } from 'components/Modal'
import { selectTraitConfig } from 'state/reducers/app'
import CardImage from './CardImage'
import NFT_COLORS, { TRAIT_ICON_MAP, TRAIT_MAP } from './NFT_COLORS'
import useFetchNftDetail from '../hooks/useFetchNftDetail'

const Row = styled(Box)`
  position: relative;
  width: 100%;
  padding: 50px 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
`
const StyledFlex = styled(Flex)`
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100%;
`

const ClippedWrapper = styled(Flex)`
  width: 100%;
  height: 100%;
  margin: 0px;
  clip-path: polygon(
    86px 0,
    calc(100% - 18px) 0,
    100% 18px,
    100% calc(100% - 22px),
    calc(100% - 18px) 100%,
    16px 100%,
    0 calc(100% - 16px),
    0 26px,
    20px 18px,
    69px 18px
  );
  position: absolute;
  background: ${(props) => props.bgColor};
  opacity: 0.6;
  &::after {
    content: '';
    display: block;
    position: absolute;
    top: 3px;
    left: 3px;
    height: calc(100% - 6px);
    width: calc(100% - 6px);
    clip-path: polygon(
      84.5px 0,
      calc(100% - 16.5px) 0,
      100% 16.5px,
      100% calc(100% - 20.5px),
      calc(100% - 16.5px) 100%,
      14.5px 100%,
      0 calc(100% - 14.5px),
      0 25.5px,
      18.5px 18px,
      67.5px 18px
    );
    background: ${(props) => props.bgColorAfter};
  }
`
const CyblocImageWrapper = styled(Flex)`
  width: 34%;
  min-width: 300px;
  max-width: 400px;
  margin-top: 0px;
  justify-content: center;
  @media (max-width: 785px) {
    min-width: 100px;
    margin-top: 50px;
    width: 80%;
  }
`

const StyledHeaderText = styled(Text)`
  font-weight: 700;
  color: #fdf9eb;
  font-size: 1.3rem;
  line-height: 24px;
  letter-spacing: 2px;
  display: inline-block;
  position: absolute;
  top: ${(props) => props.top};
  left: ${(props) => props.left};
`

const CyblocInfoWrapper = styled(Flex)`
  flex-direction: column;
  justify-content: flex-start;
  width: 66%;
  max-width: 487px;

  padding-left: 80px;
  margin-top: 0px;

  @media (max-width: 785px) {
    padding-left: 0px;
    margin-top: 50px;
  }
  @media (max-width: 650px) {
    padding-left: 0px;
    margin-top: 50px;
    width: 90%;
  }
`
const PrimaryText = styled(Text)`
  font-weight: 700;
  position: relative;
  z-index: 2;
  font-size: 2.5rem;
  color: #f3f3f3;
  letter-spacing: 2px;
  top: ${(props) => props.top};
  left: ${(props) => props.left};
`
const SubText = styled(Text)`
  font-weight: 500;
  width: 300px
  position: relative;
  z-index: 2;
  font-size: 1.3rem;
  color: #f3f3f3;
  letter-spacing: 2px;
  top: ${(props) => props.top};
  left: ${(props) => props.left};
`
const MiniText = styled(SubText)`
  font-size: 1.3rem;
`
const TraitImage = styled.img`
  width: 39.7px;
  height: 33.7px;
  z-index: 2;
  margin-right: 15px;
`
const NumberFrame = styled(Box)`
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  background-image: url(${(props) => props.url});
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
`
const InputFrame = styled(Flex)`
  width: calc(100% - 47px);
`

const SkillMainText = styled(Text)`
  font-size: 1.3rem;
  font-weight: 500;
  color: #ece9df;
  letter-spacing: 1.65px;
  z-index: 2;
`
const SkillSubText = styled(SkillMainText)`
  font-size: 1.3rem;
  z-index: 2;
`
const StyledInput = styled(Box)`
  background-color: #565260;
  border-radius: 10px;
  width: 100%%;
  height: 5px;
  position: relative;
  text-align: left;

  @media (max-width: 576px) {
    height: 8px;
  }
`

const StyledPercentage = styled.span`
  width: ${(props) => props.percentage}%;
  display: block;
  height: 100%;
  background-color: #bcbfc3;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
`
const StyledSkillInfoBlock = styled(Flex)`
  width: 30%;
`

const Bg = styled(Box)`
  width: '100%';
  position: relative;
`
const dot = keyframes`
  50% {
    transform: translateX(96px);
  }
`
const dots = keyframes`
  50% {
    transform: translateX(-31px);
  }
`

const LoaderWrapper = styled(Box)`
  position: absolute;
  top: 50%;
  left: 50%;
  width: 142px;
  height: 40px;
  margin: -20px 0 0 -71px;
  background: transparent;
  filter: contrast(20);
`

const Dot = styled(Box)`
  position: absolute;
  width: 16px;
  height: 16px;
  top: 12px;
  left: 15px;
  background: white;
  border-radius: 50%;
  transform: translateX(0);
  animation: ${dot} 2.8s infinite;
`

const Dots = styled(Box)`
  transform: translateX(0);
  margin-top: 12px;
  margin-left: 31px;
  animation: ${dots} 2.8s infinite;
`
const DotInner = styled.span`
  display: block;
  float: left;
  width: 16px;
  height: 16px;
  margin-left: 16px;
  background: white;
  border-radius: 50%;
`

const CloseImage = styled.img`
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  color: #f3f0fe;
  width: 40px;
  height: 40px;
`

const Button = styled(Box)`
  display: flex;
  justify-content: center;
  position: relative;
  align-items: flex-start;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  padding: 15px 10px;
  border-radius: 2px;
  width: 300px;
  margin: 0 auto;
  transition: 0.3s all ease-out;
  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:hover {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    font-weight: 700;
  }
`

const Loader = () => {
  return (
    <Box height="200px">
      <LoaderWrapper>
        <Dot />
        <Dots>
          <DotInner />
          <DotInner />
          <DotInner />
        </Dots>
      </LoaderWrapper>
    </Box>
  )
}

const NFTDetail = ({ onDismiss, onRefetching }) => {
  const { account } = useWeb3React()
  const tokenId = useSelector(selectSelectedNftId)
  const traitsConfig = useSelector(selectTraitConfig)
  const [data, isLoading] = useFetchNftDetail(tokenId)
  const { skills, type, traits, image } = data
  const { shooting, crossing, physical, dribbling, passing, tackling, overall } = skills
  const [onPresentSaleModal] = useModal(<SaleModal nftId={tokenId} onRefetching={onRefetching} />)

  const TRAITS_TYPES_CONFIG = useMemo(() => {
    return get(traitsConfig, 'traitTypes', [])
  }, [traitsConfig])

  const TRAITS_CONFIG = useMemo(() => {
    return get(traitsConfig, 'traits', [])
  }, [traitsConfig])

  const getTraitName = (id) => {
    const foundedTraitObj = find(TRAITS_TYPES_CONFIG, (traitObj) => {
      return traitObj.id === id
    })

    return get(foundedTraitObj, 'name', '')
  }

  const getTraitSecondTrait = (id) => {
    const foundedTraitObj = find(TRAITS_CONFIG, (traitObj) => {
      return traitObj.id === id
    })

    return get(foundedTraitObj, 'name', '')
  }

  const formatAddress = (address) => {
    if (address) {
      const addressArr = address.split('')
      return `${addressArr.slice(0, 4).join('')}...${addressArr.slice(-4).join('')}`
    }

    return null
  }

  const NFT_DETAIL_CONFIG = useMemo(() => {
    if (type) {
      return NFT_COLORS[type]
    }
    return {}
  }, [type])

  return (
    <Row>
      <CloseImage
        src="/images/close.png"
        onClick={() => {
          onDismiss()
        }}
      />
      {isLoading ? (
        <Loader />
      ) : (
        <>
          <StyledFlex>
            <CyblocImageWrapper>
              <CyBlocCard data={data} />
            </CyblocImageWrapper>
            <CyblocInfoWrapper>
              <Bg style={{ minHeight: '90px' }}>
                <StyledHeaderText top="-6px" left="6px">
                  About
                </StyledHeaderText>
                <ClippedWrapper
                  bgColor={NFT_DETAIL_CONFIG.backgroundColor}
                  bgColorAfter={NFT_DETAIL_CONFIG.backgroundColorAfter}
                />

                <Box style={{ margin: '30px 10px 14px 40px', position: 'relative' }}>
                  {/* <CountryImage src="/images/germany.png" /> */}
                  <PrimaryText>
                    #{tokenId} - {NFT_DETAIL_CONFIG.name}
                  </PrimaryText>
                  <SubText mt="14px">Owned by - {formatAddress(account)}</SubText>
                </Box>
              </Bg>

              <Bg mt="25px">
                <StyledHeaderText top="-6px" left="6px">
                  Skills
                </StyledHeaderText>
                <ClippedWrapper
                  bgColor={NFT_DETAIL_CONFIG.backgroundColor}
                  bgColorAfter={NFT_DETAIL_CONFIG.backgroundColorAfter}
                />
                <Flex alignItems="center" justifyContent="center" width="100%" mt="11px">
                  <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="50px" height="50px">
                    <SkillMainText>{overall}</SkillMainText>
                  </NumberFrame>
                  <Flex flexDirection="column" ml="10px">
                    <SkillMainText mb="5px">Overall</SkillMainText>
                    <StyledInput>
                      <StyledPercentage percentage={overall} />
                    </StyledInput>
                  </Flex>
                </Flex>

                <Flex justifyContent="space-between" margin="12px">
                  <StyledSkillInfoBlock alignItems="center" justifyContent="center">
                    <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                      <SkillSubText>{shooting}</SkillSubText>
                    </NumberFrame>
                    <InputFrame flexDirection="column">
                      <SkillSubText mb="5px">Shooting</SkillSubText>
                      <StyledInput>
                        <StyledPercentage percentage={shooting} />
                      </StyledInput>
                    </InputFrame>
                  </StyledSkillInfoBlock>
                  <StyledSkillInfoBlock alignItems="center">
                    <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                      <SkillSubText>{crossing}</SkillSubText>
                    </NumberFrame>
                    <InputFrame flexDirection="column">
                      <SkillSubText mb="5px">Crossing</SkillSubText>
                      <StyledInput>
                        <StyledPercentage percentage={crossing} />
                      </StyledInput>
                    </InputFrame>
                  </StyledSkillInfoBlock>
                  <StyledSkillInfoBlock alignItems="center" justifyContent="center">
                    <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                      <SkillSubText>{physical}</SkillSubText>
                    </NumberFrame>
                    <InputFrame flexDirection="column">
                      <SkillSubText mb="5px">Physical</SkillSubText>
                      <StyledInput>
                        <StyledPercentage percentage={physical} />
                      </StyledInput>
                    </InputFrame>
                  </StyledSkillInfoBlock>
                </Flex>
                <Flex justifyContent="space-between" margin="12px">
                  <StyledSkillInfoBlock alignItems="center" justifyContent="center">
                    <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                      <SkillSubText>{dribbling}</SkillSubText>
                    </NumberFrame>
                    <InputFrame flexDirection="column">
                      <SkillSubText mb="5px">Dribbling</SkillSubText>
                      <StyledInput>
                        <StyledPercentage percentage={dribbling} />
                      </StyledInput>
                    </InputFrame>
                  </StyledSkillInfoBlock>
                  <StyledSkillInfoBlock alignItems="center">
                    <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                      <SkillSubText>{passing}</SkillSubText>
                    </NumberFrame>
                    <InputFrame flexDirection="column">
                      <SkillSubText mb="5px">Passing</SkillSubText>
                      <StyledInput>
                        <StyledPercentage percentage={passing} />
                      </StyledInput>
                    </InputFrame>
                  </StyledSkillInfoBlock>
                  <StyledSkillInfoBlock alignItems="center" justifyContent="center">
                    <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                      <SkillSubText>{tackling}</SkillSubText>
                    </NumberFrame>
                    <InputFrame flexDirection="column">
                      <SkillSubText mb="5px">Tackling</SkillSubText>
                      <StyledInput>
                        <StyledPercentage percentage={tackling} />
                      </StyledInput>
                    </InputFrame>
                  </StyledSkillInfoBlock>
                </Flex>
              </Bg>

              <Bg mt="25px">
                <StyledHeaderText top="-6px" left="6px">
                  Traits
                </StyledHeaderText>
                <ClippedWrapper
                  bgColor={NFT_DETAIL_CONFIG.backgroundColor}
                  bgColorAfter={NFT_DETAIL_CONFIG.backgroundColorAfter}
                />
                <Flex flexDirection="column" margin="30px 10px 12px 10px">
                  {traits.map(({ type: traitType, id }, index) => {
                    return (
                      <Flex key={id} alignItems="center" mb="5px">
                        <TraitImage src={TRAIT_ICON_MAP[traitType]} />
                        <SubText width="25%" mr="20px">
                          {getTraitName(traitType)}#{id}
                        </SubText>
                        <MiniText>{getTraitSecondTrait(id)}</MiniText>
                      </Flex>
                    )
                  })}
                </Flex>
              </Bg>
            </CyblocInfoWrapper>
          </StyledFlex>
          <Button onClick={onPresentSaleModal}>
            <SubText>Sell Now</SubText>
          </Button>
        </>
      )}
    </Row>
  )
}

export default NFTDetail
