import React, { useEffect, useState, useMemo } from 'react'
import ReactTooltip from 'react-tooltip'
import { toast } from 'react-toastify'
import LoaderIcon from 'components/LoaderIcon'
import Flex from 'components/Flex'
import Box from 'components/Box'
import { ReactComponent as InfoIcon } from 'assets/images/Info.svg'
import Dropdown from 'components/Dropdown'
import { getDecimalAmount } from 'utils/formatBalance'
import { convertTimeUnitToSeconds, DAYS, HOURS } from 'utils/getTimePeriods'
import BigNumber from 'bignumber.js'
import {
  SubText,
  InputRightWrapper,
  InputRight,
  InputLeftWrapper,
  InputLeft,
  BaseText,
  Button,
  ErrorText,
} from './styles'

const SecondTab = ({
  payTokens,
  onHandleListing,
  isLoading,
  marketFee,
  isApproved,
  onHandleApprove,
  isLoadingApproved,
}) => {
  const [startPriceCurrency, setStartPriceCurrency] = useState(null)
  const [endPriceCurrency, setEndPriceCurrency] = useState(null)
  const [durationUnit, setDurationUnit] = useState(null)
  const [startPriceValue, setStartPriceValue] = useState('')
  const [endPriceValue, setEndPriceValue] = useState('')
  const [durationValue, setDurationValue] = useState('')
  const [priceError, setPriceError] = useState(false)
  const [startPriceError, setStartPriceError] = useState(false)
  const [endPriceError, setEndPriceError] = useState(false)
  const [durationError, setDurationError] = useState(false)

  const DURATION_OPTIONS = useMemo(() => {
    return [
      {
        symbol: HOURS,
      },
      {
        symbol: DAYS,
      },
    ]
  }, [])

  useEffect(() => {
    if (payTokens && payTokens.length) {
      setStartPriceCurrency(payTokens[0])
      setEndPriceCurrency(payTokens[0])
      setDurationUnit(DURATION_OPTIONS[0])
    }
  }, [])

  const onListing = () => {
    const isPriceCorrect = new BigNumber(startPriceValue).comparedTo(endPriceValue) === -1

    if (isLoading) {
      return
    }

    if (!startPriceValue) {
      setStartPriceError(true)
    }

    if (!endPriceValue) {
      setEndPriceError(true)
    }

    if (!durationValue) {
      setDurationError(true)
    }

    if (isPriceCorrect) {
      setPriceError(true)
    }

    if (!startPriceValue || !endPriceValue || !durationValue || isPriceCorrect) {
      return
    }
    const timeInseconds = convertTimeUnitToSeconds(durationValue, durationUnit.symbol)
    const { decimals, address } = startPriceCurrency
    const startPriceAmount = getDecimalAmount(startPriceValue, decimals)
    const endPriceAmount = getDecimalAmount(endPriceValue, decimals)
    if (timeInseconds < 60) {
      toast.error('Duration must be larger than 1 minute')
      return
    }
    onHandleListing(startPriceAmount.toString(), endPriceAmount.toString(), address, timeInseconds)
    setStartPriceError(false)
    setEndPriceError(false)
    setDurationError(false)
    setPriceError(false)
  }

  return (
    <>
      <SubText mb="12px">Starting price</SubText>
      <Flex mb="20px">
        <Dropdown
          options={payTokens}
          wrapperWidth="30%"
          value={startPriceCurrency}
          onHandleChange={(value) => {
            setStartPriceCurrency(value)
          }}
        />
        <Box width="65%">
          <InputRightWrapper>
            <InputRight
              placeholder="Amount"
              value={startPriceValue}
              onChange={(e) => {
                if (!isNaN(Number(e.target.value))) {
                  setStartPriceValue(e.target.value)
                }
              }}
            />
          </InputRightWrapper>
          {startPriceError && <ErrorText ml="20px">This field is required</ErrorText>}
        </Box>
      </Flex>
      <SubText mb="12px">Ending price</SubText>
      <Flex mb="20px">
        <Dropdown disabled options={payTokens} wrapperWidth="30%" value={startPriceCurrency} />
        <Box width="65%">
          <InputRightWrapper>
            <InputRight
              placeholder="Amount"
              value={endPriceValue}
              onChange={(e) => {
                if (!isNaN(Number(e.target.value))) {
                  setEndPriceValue(e.target.value)
                }
              }}
            />
          </InputRightWrapper>
          {endPriceError && <ErrorText ml="20px">This field is required</ErrorText>}
          {priceError && <ErrorText ml="20px">Ending price must be smaller than starting price</ErrorText>}
        </Box>
      </Flex>
      <SubText mb="12px">Duration</SubText>
      <Flex mb="20px">
        <Box width="55%">
          <InputLeftWrapper>
            <InputLeft
              placeholder={`${durationUnit && durationUnit.symbol}`}
              onChange={(e) => {
                if (!isNaN(Number(e.target.value))) {
                  setDurationValue(e.target.value)
                }
              }}
              value={durationValue}
            />
          </InputLeftWrapper>
          {durationError && <ErrorText>This field is required</ErrorText>}
        </Box>
        <Dropdown
          position="left"
          wrapperWidth="45%"
          options={DURATION_OPTIONS}
          value={durationUnit}
          onHandleChange={(value) => {
            setDurationUnit(value)
          }}
        />
      </Flex>
      <Flex alignItems="center" mb="20px">
        <SubText mr="5px">Fees</SubText>
        <InfoIcon data-tip data-for="feeinfo" />
        <ReactTooltip id="feeinfo">
          <span>A 2% marketplace fee will be taken from the final sale price</span>
        </ReactTooltip>
      </Flex>
      <Flex justifyContent="space-between" mb="10px">
        <BaseText fontSize="12px">Marketplace fee</BaseText>
        <BaseText fontSize="12px">{marketFee}%</BaseText>
      </Flex>
      <Flex justifyContent="center">
        {isApproved ? (
          <Button onClick={onListing}>
            {isLoading || isLoadingApproved ? (
              <LoaderIcon margin="0px" outerWidth="20px" innerWidth="20px" />
            ) : (
              <BaseText fontSize="2rem">SELL</BaseText>
            )}
          </Button>
        ) : (
          <Button onClick={onHandleApprove}>
            {isLoading || isLoadingApproved ? (
              <LoaderIcon margin="0px" outerWidth="20px" innerWidth="20px" />
            ) : (
              <BaseText fontSize="2rem">APPROVE TOKEN</BaseText>
            )}
          </Button>
        )}
      </Flex>
    </>
  )
}

export default SecondTab
