import React, { useMemo, forwardRef } from 'react'
import Slider from 'react-slick'
import { ForwardIcon, BackIcon } from './styles'

const NextArrow = ({ className, style, onClick, index, disabled }) => {
  const IMAGE_MAP = {
    0: '/images/arrow_common_right.png',
    1: '/images/arrow_rare_right.png',
    2: '/images/arrow_epic_right.png',
  }

  return (
    <ForwardIcon
      className={className}
      onClick={() => {
        if (!disabled) {
          onClick()
        }
      }}
      disabled={disabled}
      src={IMAGE_MAP[index]}
      style={{
        ...style,
        display: 'block',
      }}
    />
  )
}

const BackArrow = ({ className, style, onClick, index, disabled }) => {
  const IMAGE_MAP = {
    0: '/images/arrow_common_left.png',
    1: '/images/arrow_rare_left.png',
    2: '/images/arrow_epic_left.png',
  }
  return (
    <BackIcon
      disabled={disabled}
      src={IMAGE_MAP[index]}
      className={className}
      onClick={() => {
        if (!disabled) {
          onClick()
        }
      }}
      style={{
        ...style,
        display: 'block',
      }}
    />
  )
}

const SliderComponent = forwardRef(
  ({ children, settings, index, loading, beforeChange, lazyLoad = null, ...props }, ref) => {
    const DEFAULT_SETTINGS = useMemo(() => {
      return {
        dots: false,
        infinite: true,
        fade: true,
        draggable: false,
        swipe: false,
        speed: 500,
        cssEase: 'cubic-bezier(0.165, 0.840, 0.440, 1.000)',
        slidesToShow: 1,
        slidesToScroll: 1,
        nextArrow: <NextArrow index={index} disabled={loading} />,
        prevArrow: <BackArrow index={index} disabled={loading} />,
        beforeChange,
      }
    }, [index, loading])

    return (
      <Slider {...DEFAULT_SETTINGS} ref={ref} {...props} arrow={false} lazyLoad={lazyLoad}>
        {children}
      </Slider>
    )
  },
)

export default SliderComponent
