import React from 'react'
import Box from 'components/Box'
import LoaderIcon from 'components/LoaderIcon'
import Flex from 'components/Flex'
import Text from 'components/Text'
import styled from 'styled-components'
import Card from '../NFTCard'

const GridContainer = styled(Box)`
  width: 100%;
  display: grid;

  grid-template-columns: repeat(auto-fill, minmax(202px, 1fr));
  grid-auto-rows: 1fr;
  column-gap: 43px;
  row-gap: 43px;
`
const LoaderWrapper = styled(Flex)`
  width: 100%;
  height: calc(100vh - 290px);
  justify-content: center;
  position: absolute;
  align-items: center;
`

const StyledHeaderText = styled(Text)`
  width: 100%;

  font-size: ${(props) => (props.isActive ? '3rem' : '1.4rem')};
  line-height: 48px;
  letter-spacing: 2px;
  font-weight: 700;
  color: #f3f3f3;
  cursor: pointer;
  padding: 30px 0px;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  transition: 0.3s opacity ease-out;
  text-align: center;
  @media (max-width: 600px) {
    padding: 0px;
  }
`

const NFT = styled(Box)`
  // margin: 10px;
`

const NFTsGrid = ({ mt = '0px', items = [], numPerRow, loading, category, onClickItem }) => {
  // const n = numPerRow || 6

  return loading ? (
    <LoaderWrapper>
      <LoaderIcon outerWidth="60px" innerWidth="45px" />
    </LoaderWrapper>
  ) : items.length === 0 ? (
    <StyledHeaderText>No CyBlocs to display</StyledHeaderText>
  ) : (
    <GridContainer mt={mt}>
      {items.map((item) => {
        return (
          <NFT onClick={() => onClickItem(item)} key={item._id}>
            <Card item={item} />
          </NFT>
        )
      })}
    </GridContainer>
  )
}

export default NFTsGrid
