import React, { useState, useContext, useCallback, useMemo } from 'react'
import { useSelector } from 'react-redux'
import get from 'lodash/get'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import Autocomplete from 'components/Autocomplete'
import { Collapse } from 'react-collapse'
import styled from 'styled-components'
import {
  selectFilters,
  selectTraitFilter,
  selectTraitType,
  setFilters,
  TRAIT_TYPE,
} from 'pages/Marketplace/store/reducer'
import { selectTraitConfig } from 'state/reducers/app'
import { MarketplaceContext } from '../../context/MarketplaceContext'
import DropDownFilter from '../DropDownFilter'
import SearchInput from '../../../../components/SearchInput'

const Content = styled(Flex)`
  padding: 45px 0px 23px 24px;
  flex-direction: column;
`
const Button = styled.button`
  position: relative;
  width: 100%;
  height: 48px;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: transparent;
  border: none;
  cursor: pointer;
`

const FilterButton = styled(Button)`
  margin-top: 2rem;
  background-image: ${(props) =>
    props.selected ? "url('/images/BgFilterButtonSeleted.png')" : "url('/images/BgFilterButton.png')"};
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  // transition: background-image 0.3s ease-in, transform 0.1s ease-in, opacity: 0.3s ease-in;

  &:hover {
    // opacity: 1;
  }
  & > div {
    opacity: ${(props) => (props.selected ? '1' : '0.5')};
  }
  & > img {
    opacity: ${(props) => (props.selected ? '1' : '0.5')};
  }
  z-index: 99;
`

const IconImage = styled.img`
  width: 2.9rem;
  height: 2.9rem;
  object-fit: contain;
  position: absolute;
  right: 9px;
  top: 9px;
`
const Title = styled(Text)`
  font-size: 1.6rem;
  color: #e0e0e0;
  line-height: 17px;
  letter-spacing: 2px;
  font-weight: bold;
`
const WrapperListContent = styled(Box)`
  width: 100%;
  max-height: 400px;
  padding-right: 20px;
  overflow-y: scroll;
`

const TraitFilter = ({ setQuery }) => {
  const { state, marketplaceDispatch } = useContext(MarketplaceContext)
  const [isOpen, setIsOpen] = useState(true)
  const [searchValue, setSearchValue] = useState('')
  const traitFilter = useSelector(() => selectTraitFilter(state))
  const traitType = useSelector(() => selectTraitType(state))
  const filters = useSelector(() => selectFilters(state))
  const traits = useSelector(selectTraitConfig)

  const TRAITS_TYPES_CONFIG = useMemo(() => {
    return get(traits, 'traitTypes', [])
  }, [traits])

  const TRAITS_CONFIG = useMemo(() => {
    return get(traits, 'traits', [])
  }, [traits])

  const onHandleSelectType = (traitId, type) => {
    let newTraitFilter
    const found = traitFilter.find((el) => Number(el) === traitId)
    if (found) {
      newTraitFilter = traitFilter.filter((el) => Number(el) !== traitId)
    } else {
      newTraitFilter = [traitId]
    }
    marketplaceDispatch(
      setFilters({
        ...filters,
        trait: {
          type,
          traitFilteredList: newTraitFilter,
        },
        page: {
          ...filters.page,
          curPage: 0,
        },
      }),
    )
    if (type === TRAIT_TYPE.NAME) {
      setQuery({
        traitType: newTraitFilter,
        pageNumber: 1,
      })
    } else {
      setQuery({
        traitId: newTraitFilter,
        pageNumber: 1,
      })
    }
  }

  const onChangeState = () => {
    setIsOpen(!isOpen)
  }
  return (
    <DropDownFilter mt="46px" isOpen={isOpen} title="Trait" onChangeState={onChangeState}>
      <Collapse isOpened={isOpen}>
        <Content>
          <Autocomplete
            onSearch={() => {
              const foundTrait = TRAITS_CONFIG.find((el) => el.name === searchValue)
              if (foundTrait) {
                onHandleSelectType(foundTrait.id, TRAIT_TYPE.ID)
              }
            }}
            suggestions={TRAITS_CONFIG.map((trait) => trait.name)}
            value={searchValue}
            onChangeValue={(val) => {
              setSearchValue(val)
            }}
          />
          <WrapperListContent>
            {TRAITS_TYPES_CONFIG.map((trait) => (
              <FilterButton
                key={trait.id}
                selected={traitFilter.map((t) => Number(t)).includes(trait.id) && traitType === TRAIT_TYPE.NAME}
                onClick={() => {
                  onHandleSelectType(trait.id, TRAIT_TYPE.NAME)
                }}
              >
                <Title>{trait.name}</Title>
                {/* <IconImage src={trait.icon} /> */}
              </FilterButton>
            ))}
          </WrapperListContent>
        </Content>
      </Collapse>
    </DropDownFilter>
  )
}
export default TraitFilter
