import React, { useEffect, useMemo, useState } from 'react'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import Loader from 'react-loader-spinner'
import { toast } from 'react-toastify'
import { fromUnixTime } from 'date-fns'
import queryString from 'query-string'
import { useWeb3React } from '@web3-react/core'
import { useHistory, useRouteMatch } from 'react-router-dom'
import { useModal, CardDetailModal, OpenPackModal } from 'components/Modal'
import {
  selectCyblocList,
  selectCyblocLoading,
  setCyblocList,
  setNewCylocList,
  setSelectedNftId,
} from 'state/reducers/cybloc'
import Box from 'components/Box'
import Flex from 'components/Flex'
import Text from 'components/Text'
import PageLoading from 'components/PageLoading'
import useOpenPackage from 'hooks/useOpenPackage'
import differenceWith from 'lodash/differenceWith'
import isEqual from 'lodash/isEqual'
import { useAppDispatch } from 'state'
import { selectContractAddress } from 'state/reducers/app'
import { NUMBER_OF_PACKAGE_OPEN } from 'config/constant'
import useCanUnpack from 'hooks/useCanUnpack'
import { CommonPackageCard, RarePackageCard, EpicPackageCard } from './components/PackageCard'
import { getCyblocList } from './hooks/useGetCyblocList'
import { getPackInfoFromTokenId } from './utils/getPackInfoFromTokenId'

const Wrapper = styled(Box)`
  background-color: #0f0a18;
  min-height: inherit;
`
const Row = styled(Box)`
  max-width: 1000px;
  margin: 0 auto;

  @media (max-width: 1100px) {
    max-width: 800px;
  }

  @media (max-width: 800px) {
    max-width: 80%;
  }

  @media (max-width: 576px) {
    max-width: 350px;
  }
`
const StyledHeaderContent = styled(Flex)`
  justify-content: space-between;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 40px;
  }
`

const StyledHeaderText = styled(Text)`
  font-size: ${(props) => (props.isActive ? '3rem' : '1.4rem')};
  line-height: 48px;
  letter-spacing: 2px;
  font-weight: 700;
  color: #f3f3f3;
  cursor: pointer;
  padding: 30px 0px;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  transition: 0.3s opacity ease-out;

  @media (max-width: 600px) {
    padding: 0px;
  }
`
const OpenPackButton = styled.button`
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: transparent;
  border: none;
  background-image: url('/images/open-pack-frame.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in, opacity: 0.3s ease-in;
  padding: 15px 20px;
  border-radius: 2px;
  opacity: ${(props) => (props.disabled ? '0.2' : '1')};
  @media (max-width: 768px) {
    width: 100%;
  }

  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
    opacity: ${(props) => (props.disabled ? '0.2' : '1')};
  }

  &:active:enabled {
    transform: translateY(-2px);
    opacity: 80%;
  }

  &:not(:last-of-type) {
    margin-right: 20px;

    @media (max-width: 768px) {
      margin-right: 0px;
      margin-bottom: 20px;
    }
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`
const Grid = styled(Box)`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 40px;
  row-gap: 40px;

  @media (max-width: 1100px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (max-width: 580px) {
    grid-template-columns: 1fr;
  }
`

const Empty = styled(Box)`
  width: 100%;
  display: flex;
  justify-content: center;
`
const Inventory = ({ packageData, isLoading }) => {
  const {
    CYBLOC_PACK: { ADDRESS: PACK_ADDRESS },
    CYBLOC_UTILS: { ADDRESS: UTIL_ADDRESS },
  } = useSelector(selectContractAddress)
  const cyblocList = useSelector(selectCyblocList)
  const [isLoadingOpenPack, setIsLoadingOpenPack] = useState(false)
  const isLoadingCyblocList = useSelector(selectCyblocLoading)
  const dispatch = useAppDispatch()
  const history = useHistory()
  const [selectedPackage, setSelectedPackage] = useState({
    type: null,
    packageIndex: [],
  })
  const { path } = useRouteMatch()
  const { account } = useWeb3React()
  // Modal to open multiple packs
  const [onPresentOpenPackModal] = useModal(<OpenPackModal packageData={packageData} history={history} />)
  const { onUnpack } = useOpenPackage(PACK_ADDRESS)
  const { canUnpack } = useCanUnpack(UTIL_ADDRESS, PACK_ADDRESS)
  const loading = useMemo(() => {
    return isLoading || isLoadingOpenPack || isLoadingCyblocList
  }, [isLoading, isLoadingOpenPack, isLoadingCyblocList])

  useEffect(() => {
    dispatch(setNewCylocList([]))
  }, [])

  const { commonPackageData, rarePackageData, epicPackageData } = packageData
  const onNavigateToOpen = (packageType, packageIndex) => {
    const stringtifiedQuery = queryString.stringify(
      {
        packageIndex,
      },
      {
        arrayFormat: 'bracket',
      },
    )
    history.push(`${path}/package-opening/${packageType}?${stringtifiedQuery}`)
  }

  const onHandleSelectPackage = (packageType, index) => {
    const { type, packageIndex } = selectedPackage
    if (type === packageType) {
      if (packageIndex.includes(index)) {
        setSelectedPackage({
          ...selectedPackage,
          packageIndex: packageIndex.filter((i) => i !== index),
        })
      } else if (packageIndex.length < NUMBER_OF_PACKAGE_OPEN) {
        setSelectedPackage({
          ...selectedPackage,
          packageIndex: [...packageIndex, index],
        })
      }
    } else {
      setSelectedPackage({
        type: packageType,
        packageIndex: [index],
      })
    }
  }

  // Function to open selected package
  const onHandleOpenSelectedPackage = async () => {
    try {
      // const isProductionEndpoint = location.host.indexOf('genesis.cyball.com') === 0
      // const expectedDate = fromUnixTime(1637672400)
      // if (new Date().getTime() < 1637697600000 && isProductionEndpoint) {
      //   toast.error(`Wait till ${expectedDate} to open`)
      //   return
      // }
      setIsLoadingOpenPack(true)
      const { type, packageIndex } = selectedPackage
      // Get pack info based on selected pack index
      const packsInfo = getPackInfoFromTokenId(packageIndex, [
        ...commonPackageData,
        ...rarePackageData,
        ...epicPackageData,
      ])
      const isLocked = await canUnpack(packsInfo)
      const result = await onUnpack(packsInfo, isLocked)
      if (result.status) {
        // Successfully open
        const data = await getCyblocList(account)
        if (data) {
          const { cyblocs } = data
          const newEls = differenceWith(cyblocs, cyblocList, isEqual)
          dispatch(setNewCylocList(newEls))
          dispatch(setCyblocList(cyblocs))
          setIsLoadingOpenPack(false)
          onNavigateToOpen(type, packageIndex)
        }
      }
    } catch (error) {
      setIsLoadingOpenPack(false)
      if (error && error.code === 4001) {
        return
      }
      toast.error('Fail to open packages', {
        hideProgressBar: true,
      })
    }
  }
  return (
    <Wrapper>
      <Row>
        <StyledHeaderContent>
          <Flex alignItems="baseline">
            <StyledHeaderText mr="15px" isActive>
              Packs
            </StyledHeaderText>
          </Flex>
          <Flex flexWrap="wrap">
            <OpenPackButton
              disabled={selectedPackage.packageIndex.length === 0 || isLoadingOpenPack}
              onClick={onHandleOpenSelectedPackage}
            >
              <Text>Open selected packs</Text>
            </OpenPackButton>
            {/* <OpenPackButton
              onClick={onPresentOpenPackModal}
              disabled={
                (commonPackageData.length === 0 && rarePackageData.length === 0 && epicPackageData.length === 0) ||
                selectedPackage.packageIndex.length > 0 ||
                loading
              }
            >
              <Text>Open multiple packs</Text>
            </OpenPackButton> */}
          </Flex>
        </StyledHeaderContent>
        {loading ? (
          <PageLoading />
        ) : (
          <>
            {commonPackageData.length === 0 && rarePackageData.length === 0 && epicPackageData.length === 0 ? (
              <Empty>
                <StyledHeaderText>No packs to display</StyledHeaderText>
              </Empty>
            ) : (
              <Grid>
                {commonPackageData.map(({ tokenId }) => {
                  return (
                    <CommonPackageCard
                      data={tokenId}
                      onClick={() => onHandleSelectPackage('1', tokenId)}
                      selectedPackage={selectedPackage}
                    />
                  )
                })}
                {rarePackageData.map(({ tokenId }) => {
                  return (
                    <RarePackageCard
                      data={tokenId}
                      onClick={() => onHandleSelectPackage('2', tokenId)}
                      selectedPackage={selectedPackage}
                    />
                  )
                })}
                {epicPackageData.map(({ tokenId }) => {
                  return (
                    <EpicPackageCard
                      data={tokenId}
                      onClick={() => onHandleSelectPackage('3', tokenId)}
                      selectedPackage={selectedPackage}
                    />
                  )
                })}
              </Grid>
            )}
          </>
        )}
      </Row>
    </Wrapper>
  )
}

export default Inventory
