import React, { useContext, useEffect, useState, useMemo } from 'react'
import Skeleton from 'react-loading-skeleton'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import get from 'lodash/get'
import Flex from 'components/Flex'
import Text from 'components/Text'

import {
  selectFilters,
  selectFetching,
  resetFilters,
  selectClassFilter,
  selectTraitFilter,
  setFilters,
  selectTotalItems,
  selectIdFilter,
  TRAIT_TYPE,
  selectTraitType,
} from 'pages/Marketplace/store/reducer'
import SearchInput from 'components/SearchInput'
import { MarketplaceContext } from 'pages/Marketplace/context/MarketplaceContext'
import { selectTraitConfig } from 'state/reducers/app'
import SortFilter from './SortFilter'
import PayToken from '../PayToken'

import TabButton, { TAB_BUTTON_TYPE } from '../LeftFilter/Tabs/TabButton.js'

const Wrapper = styled(Flex)`
  margin: 5px 0px;
  flex-direction: column;

  position: relative;
  width: 100%;
`
const ResultsText = styled(Text)`
  text-align: center;
  min-width: 126px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  margin-right: 14px;
  color: #e0e0e0;
`

const TopFilter = styled(Flex)`
  position: relative;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  @media (max-width: 1000px) {
    justify-content: center;
  }
`
const ListsFilter = styled(Flex)`
  position: relative;
  flex-wrap: wrap;
  width: 100%;
`

const Title = styled(Text)`
  margin: auto 15px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  // line-height: 23px;
  /* identical to box height */
  align-items: center;
  color: #e0e0e0;
  z-index: 99;
`
const CloseImage = styled.img`
  width: 39px;
  height: 39px;
  padding: 10px 20px 10px 0px;
  z-index: 99;
`
const ButtonText = styled.button`
  margin-top: 10px;
  height: 39px;
  color: #e0e0e0;
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`
const ButtonClear = styled(Title)`
  color: #b499ff;
`
const Type = {
  ClassFilter: 1,
  TraitFillter: 2,
  IdFilter: 3,
}
const ExploreFilterHeader = ({ setQuery }) => {
  const { state, marketplaceDispatch } = useContext(MarketplaceContext)
  const loading = useSelector(() => selectFetching(state))
  const classFilter = useSelector(() => selectClassFilter(state))
  const traitFilter = useSelector(() => selectTraitFilter(state))
  const idFilter = useSelector(() => selectIdFilter(state))
  const totalItems = useSelector(() => selectTotalItems(state))
  const filters = useSelector(() => selectFilters(state))
  const traitType = useSelector(() => selectTraitType(state))
  const [searchId, setSearchId] = useState('')
  const traits = useSelector(selectTraitConfig)

  const TRAITS_TYPES_CONFIG = useMemo(() => {
    return get(traits, 'traitTypes', [])
  }, [traits])

  const TRAITS_CONFIG = useMemo(() => {
    return get(traits, 'traits', [])
  }, [traits])
  const [mapListFilter, setMapListFilter] = useState([])
  const [radioValue, setRadio] = useState(true)
  useEffect(() => {
    const class_filter = classFilter.map((el) => {
      return { type: Type.ClassFilter, id: el.id, name: el.name, timestamp: el.timestamp }
    })

    const trait_filter = traitFilter.map((id) => {
      const foundedName =
        traitType === TRAIT_TYPE.NAME
          ? TRAITS_TYPES_CONFIG.find((el) => el.id === Number(id)).name
          : TRAITS_CONFIG.find((el) => el.id === Number(id)).name
      return { type: Type.TraitFillter, id, name: foundedName }
    })

    const id_filter = idFilter
      ? [
          {
            type: Type.IdFilter,
            name: idFilter,
            timestamp: Math.floor(Date.now() / 1000),
          },
        ]
      : []

    const n_ListFilter = class_filter.concat(trait_filter, id_filter)
    setMapListFilter(n_ListFilter.sort((a, b) => a.timestamp - b.timestamp))
  }, [filters])

  const onRemoveFilteredItem = (item) => {
    const { type } = item
    if (type === Type.ClassFilter) {
      const newClassFilter = classFilter.filter((i) => item.name !== i.name)
      marketplaceDispatch(
        setFilters({
          ...filters,
          class: newClassFilter,
          page: {
            ...filters.page,
            curPage: 0,
          },
        }),
      )

      setQuery({
        classType: newClassFilter.map((el) => el.id),
        pageNumber: 1,
      })
    }

    if (type === Type.TraitFillter) {
      const newTraitFilter = traitFilter.filter((i) => item.id !== i)
      marketplaceDispatch(
        setFilters({
          ...filters,
          trait: {
            ...filters.trait,
            traitFilteredList: newTraitFilter,
          },
          page: {
            ...filters.page,
            curPage: 0,
          },
        }),
      )

      if (traitType === TRAIT_TYPE.NAME) {
        setQuery({
          traitType: undefined,
          pageNumber: 1,
        })
      }

      if (traitType === TRAIT_TYPE.ID) {
        setQuery({
          traitId: undefined,
          pageNumber: 1,
        })
      }
    }

    if (type === Type.IdFilter) {
      setSearchId('')
      marketplaceDispatch(
        setFilters({
          ...filters,
          id: '',
          page: {
            ...filters.page,
            curPage: 0,
          },
        }),
      )
      setQuery({
        pageNumber: 1,
        cyblocId: undefined,
      })
    }

    const filteredList = mapListFilter.filter((_item) => _item.name !== item.name)
    setMapListFilter(filteredList)
  }

  const onHandleClickSearch = () => {
    // Change filter info here
    marketplaceDispatch(
      setFilters({
        ...filters,
        id: searchId,
      }),
    )
  }

  const onChange = (e) => {
    const newVal = get(e, 'target.value', null)
    setSearchId(newVal)
  }

  const onKeyDown = (e) => {
    // User pressed the enter key
    if (e.keyCode === 13) {
      onHandleClickSearch()
    }
  }

  const generateFilterType = (type) => {
    switch (type) {
      case Type.ClassFilter:
        return 'Class:'
      case Type.IdFilter:
        return 'Id:'
      case Type.TraitFillter:
        return 'Trait:'
      default:
        return null
    }
  }

  return (
    <Wrapper>
      <TopFilter>
        {/* <Skeleton width={100} height={24} /> */}

        <ResultsText>{loading ? <Skeleton width={100} height={24} /> : <>{totalItems} result(s) </>}</ResultsText>
        {/* <SearchInput
          onHandleClickSearch={onHandleClickSearch}
          onChange={onChange}
          type="number"
          placeholder="Cybloc id"
          onKeyDown={onKeyDown}
          value={searchId}
          style={{
            maxWidth: 'none',
            flex: 1,
          }}
        /> */}
        <PayToken setQuery={setQuery} />
        <SortFilter setQuery={setQuery} />
      </TopFilter>

      <ListsFilter mt="10px">
        {mapListFilter.map((el) => (
          <TabButton mr="8px" mt="10px" type={TAB_BUTTON_TYPE.left} isActive>
            <Title>
              {generateFilterType(el.type)} {el.name}
            </Title>
            <CloseImage src="images/X.png" onClick={() => onRemoveFilteredItem(el)} />
          </TabButton>
        ))}
        {mapListFilter.length > 0 ? (
          <ButtonText
            onClick={() => {
              marketplaceDispatch(resetFilters())
              setQuery({
                classType: undefined,
                traitType: undefined,
                cyblocId: undefined,
                traitId: undefined,
                pageNumber: 1,
                shootingMin: undefined,
                shootingMax: undefined,
                crossingMin: undefined,
                crossingMax: undefined,
                tacklingMin: undefined,
                tacklingMax: undefined,
                passingMin: undefined,
                passingMax: undefined,
                dribblingMin: undefined,
                dribblingMax: undefined,
                physicalMin: undefined,
                physicalMax: undefined,
              })
            }}
          >
            <ButtonClear color="#B499FF" mt="30px">
              Clear all
            </ButtonClear>
          </ButtonText>
        ) : (
          <></>
        )}
      </ListsFilter>
    </Wrapper>
  )
}

export default ExploreFilterHeader
