import React from 'react'
import styled from 'styled-components'
import Text from 'components/Text'

const Wrapper = styled.div`
  height: 10px;
  width: 100%;
  background-color: #565260;
  border-radius: 40px;
  margin: 0 10px;
  position: relative;
`

const InnerWrapper = styled.div`
  height: 100%;
  width: ${(props) => props.percentage}%;
  background-color: ${(props) => props.bgcolor};
  position: relative;
  border-radius: 40px;
  text-align: right;
`

const Icon = styled.div`
  width: 17px;
  height: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
  background-image: url(${(props) => props.iconUrl});
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  right: -5px;
  top: -4px;
`

const PriceText = styled(Text)`
  color: #f3f3f3;
  font-size: 1.3rem;
  font-weight: 600;
  bottom: -25px;
  width: 100%;
  text-align: center;
  position: absolute;
`

const ProgressBar = ({ bgcolor, percentage, value, iconUrl, symbol = '' }) => {
  return (
    <Wrapper>
      <InnerWrapper bgcolor={bgcolor} percentage={percentage}>
        <Icon iconUrl={iconUrl} />
      </InnerWrapper>
      <PriceText style={{ color: '#FFE5BE' }}>
        {value} {symbol}
      </PriceText>
    </Wrapper>
  )
}

export default ProgressBar
