import { useEffect, useState } from 'react'
import axios from 'axios'
import { API_CYBALL } from 'config/constant'
import { useWeb3React } from '@web3-react/core'
import { useAppDispatch } from 'state'
import { setCyblocList, setIsLoadingCybloc } from 'state/reducers/cybloc'

export const getCyblocList = async (account) => {
  try {
    const { data } = await axios.get(`${API_CYBALL}/cyblocs`, {
      params: {
        address: account,
        limit: 100000,
      },
    })

    return data
  } catch (error) {
    console.log(error, 'Fail to fetch cybloc list')
    return null
  }
}

const useGetCyblocList = () => {
  const { account } = useWeb3React()
  const [refetching, setRefetching] = useState(0)
  const dispatch = useAppDispatch()

  const onHandleRefetchingNftList = () => {
    setRefetching(refetching + 1)
  }

  useEffect(() => {
    const fetchData = async () => {
      dispatch(setIsLoadingCybloc(true))
      const data = await getCyblocList(account)
      if (data) {
        const { cyblocs } = data
        dispatch(setCyblocList(cyblocs))
      }
      dispatch(setIsLoadingCybloc(false))
    }

    if (account) {
      fetchData()
    }
  }, [account, refetching])

  return [onHandleRefetchingNftList]
}

export default useGetCyblocList
