import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { sellNft, approveNftId } from 'utils/callHelpers'
import { useCyblocCoreContract, useCyblocMarketContract } from './useContract'

const useSellNft = (marketContractAddress, cyblocCoreAddress) => {
  const { account } = useWeb3React()
  const cyblocMarketContract = useCyblocMarketContract(marketContractAddress)
  const handleSellNft = useCallback(
    async (nftAddress, tokenId, startingPrice, endingPrice, payToken, duration) => {
      const tx = await sellNft(
        account,
        cyblocMarketContract,
        nftAddress,
        tokenId,
        startingPrice,
        endingPrice,
        duration,
        payToken,
      )
      return tx
    },
    [account, cyblocMarketContract],
  )

  return { onSell: handleSellNft }
}

export default useSellNft
