import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import useRefresh from './useRefresh'

const useIsApprovedNftId = (cyblocCoreContract, spenderAddress, tokenId) => {
  const [isApproved, setIsApproved] = useState(false)
  const { slowRefresh, fastRefresh } = useRefresh()
  const [isLoadingApproved, setIsLoadingApproved] = useState(true)
  const { account } = useWeb3React()
  const checkIsApprovedSpendingTokenId = async () => {
    try {
      const allowedAddress = await cyblocCoreContract.methods.getApproved(tokenId).call()
      if (allowedAddress && allowedAddress.toLowerCase() === spenderAddress.toLowerCase()) {
        setIsApproved(true)
        setIsLoadingApproved(false)
        return null
      }

      setIsApproved(false)
      setIsLoadingApproved(false)
      return null
    } catch (error) {
      setIsApproved(false)
      setIsLoadingApproved(false)
      console.log(error, 'fail to fetch approval status of token id')
      return null
    }
  }

  useEffect(() => {
    if (cyblocCoreContract && account) {
      checkIsApprovedSpendingTokenId()
    }
  }, [account, spenderAddress, slowRefresh])

  return [isApproved, checkIsApprovedSpendingTokenId, isLoadingApproved]
}

export default useIsApprovedNftId
