import { useCallback } from 'react'
import axios from 'axios'
import { useWeb3React } from '@web3-react/core'
import get from 'lodash/get'
import { API_CYBALL, CAMPAIGN_TYPE } from 'config/constant'
import { unPack, lock } from 'utils/callHelpers'
import { useCyblocNftContract } from './useContract'

// We will select correct contract, based on selected package
const useOpenPackage = (packageContractAddress) => {
  const { account } = useWeb3React()
  const cyblocNftContract = useCyblocNftContract(packageContractAddress)
  const handleUnpack = useCallback(
    async (packages, isLocked) => {
      const tokenIdList = packages.map((p) => p.tokenId)
      if (!isLocked) {
        const txLockHash = await lock(cyblocNftContract, tokenIdList, account)
      }
      const { data = {} } = await axios.post(
        `${API_CYBALL}/open-proof?code=${process.env.REACT_APP_AZURE_CODE}&contract=${packageContractAddress}&address=${account}`,
        {
          packs: packages,
          campaign: CAMPAIGN_TYPE.GENESIS,
        },
      )
      const { openPackData } = data
      if (openPackData) {
        const proofs = get(openPackData, 'proofs')
        const userPacks = get(openPackData, 'userPacks')
        const txHash = await unPack(cyblocNftContract, account, userPacks, proofs)
        return txHash
      }

      return null
    },
    [account, cyblocNftContract],
  )

  return { onUnpack: handleUnpack }
}

export default useOpenPackage
