import { packageConfig } from 'config/constant'
import { ethers } from 'ethers'

export const buyCyblocPack = async (cyblockPresaleContract, quantity, amount, account, proof, maxBuy) => {
  return cyblockPresaleContract.methods
    .buy(quantity, maxBuy, proof)
    .send({
      from: account,
      value: amount,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const unPack = (cyblocPackContract, account, packages, proofs) => {
  const packList = packages.map((pack) => [pack.tokenId, pack.gene])
  const proofList = proofs.map((proof) => [proof.v, proof.r, proof.s])
  return cyblocPackContract.methods
    .open(packList, proofList)
    .send({
      from: account,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const lock = (nftManagerAddress, packages, account) => {
  return nftManagerAddress.methods
    .lock(packages)
    .send({
      from: account,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const getPackageIndex = async (cyblockPresaleContract, address, startIndex, endIndex) => {
  return cyblockPresaleContract.methods.getMyPackageIndex(address, startIndex, endIndex).call()
}

export const canceledSellNft = async (account, cyblocMarketContract, nftAddress, tokenId) => {
  return cyblocMarketContract.methods
    .cancelAuction(nftAddress, tokenId)
    .send({
      from: account,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const canUnpack = async (cyblocUtilContract, packages, cyblocPackAddress) => {
  return cyblocUtilContract.methods.canUnpack(cyblocPackAddress, packages).call()
}

export const sellNft = async (
  account,
  cyblocMarketContract,
  nftAddress,
  tokenId,
  startingPrice,
  endingPrice,
  duration,
  payToken,
) => {
  return cyblocMarketContract.methods
    .createAuction(nftAddress, tokenId, startingPrice, endingPrice, duration, payToken)
    .send({
      from: account,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const approveNftId = async (address, tokenId, cyblocCoreContract, account) => {
  return cyblocCoreContract.methods
    .approve(address, tokenId)
    .send({
      from: account,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const buyNft = async (cyblocCoreContract, nftAddress, tokenId, payToken, account, amount, isNativeToken) => {
  return cyblocCoreContract.methods
    .bid(nftAddress, tokenId, amount, payToken)
    .send({
      from: account,
      ...(isNativeToken && { value: amount }),
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const getAuction = async (cyblocMarketContract, nftAddress, tokenId) => {
  return cyblocMarketContract.methods.auctions(nftAddress, tokenId).call()
}

export const getCurrentPrice = async (cyblocMarketContract, nftAddress, tokenId) => {
  return cyblocMarketContract.methods.getCurrentPrice(nftAddress, tokenId).call()
}

export const approveContract = async (tokenContract, marketContractAddress, account) => {
  const res = await tokenContract.methods.approve(marketContractAddress, ethers.constants.MaxUint256).send({
    from: account,
  })
  return res
}
