import { useMemo } from 'react'
import useWeb3 from 'hooks/useWeb3'
import {
  getCyblocPresaleContract,
  getCyblocNftContract,
  getCyblocUtilContract,
  getCyblocMarketContract,
  getCyblocCoreContract,
} from 'utils/contractHelpers'

export const useCyblockPresaleContract = (address) => {
  const web3 = useWeb3()
  return useMemo(() => getCyblocPresaleContract(web3, address), [address, web3])
}

export const useCyblocNftContract = (address) => {
  const web3 = useWeb3()
  return useMemo(() => getCyblocNftContract(web3, address), [address, web3])
}

export const useCyblocUtilContract = (address) => {
  const web3 = useWeb3()
  return useMemo(() => getCyblocUtilContract(web3, address), [address, web3])
}

export const useCyblocMarketContract = (address) => {
  const web3 = useWeb3()
  return useMemo(() => getCyblocMarketContract(web3, address), [address, web3])
}

export const useCyblocCoreContract = (address) => {
  const web3 = useWeb3()
  return useMemo(() => getCyblocCoreContract(web3, address), [address, web3])
}
