/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  openSaleLayout: false,
  isLoading: false,
  contractAddress: {
    CYBLOC_PACK_SALE_COMMON: {},
    CYBLOC_PACK_SALE_EPIC: {},
    CYBLOC_PACK_SALE_RARE: {},
    CYBLOC_PACK: {},
    CYBLOC_UTILS: {},
  },
  packProof: {
    common: {},
    rare: {},
    epic: {},
  },
  saleConfig: {
    traits: {},
    types: {},
    payToken: {},
    isLoading: true,
  },
}

export const appSlice = createSlice({
  name: 'App',
  initialState,
  reducers: {
    setOpenSaleLayout: (state, action) => {
      state.openSaleLayout = action.payload
    },
    setContractAddress: (state, action) => {
      state.contractAddress = action.payload
    },
    setPackProof: (state, action) => {
      state.packProof = action.payload
    },
    setSaleConfig: (state, action) => {
      const data = action.payload
      const [traits, types, payToken] = data
      state.saleConfig.traits = traits
      state.saleConfig.types = types
      state.saleConfig.payToken = payToken
      state.saleConfig.isLoading = false
    },
    setIsLoading: (state, action) => {
      state.isLoading = action.payload
    },
  },
})

// Actions
export const { setOpenSaleLayout, setContractAddress, setPackProof, setIsLoading, setSaleConfig } = appSlice.actions
// Selectors

export const selectOpenSaleLayout = (state) => state.app.openSaleLayout
export const selectContractAddress = (state) => state.app.contractAddress
export const selectIsLoading = (state) => state.app.isLoading
export const selectPackProof = (state) => state.app.packProof
export const selectCommonProof = (state) => state.app.packProof.common.proof
export const selectRareProof = (state) => state.app.packProof.rare.proof
export const selectEpicProof = (state) => state.app.packProof.epic.proof
export const selectSaleConfig = (state) => state.app.saleConfig
export const selectTraitConfig = (state) => selectSaleConfig(state).traits
export const selectTypeConfig = (state) => selectSaleConfig(state).types
export const selectPayTokenConfig = (state) => selectSaleConfig(state).payToken
export const selectIsLoadingConfig = (state) => selectSaleConfig(state).isLoading

export default appSlice.reducer
