import React, { useRef, useState, useEffect } from 'react'
import styled from 'styled-components'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import useClickOutside from 'hooks/useClickOutside'

const PriceWrapper = styled(Flex)`
  position: relative;
  width: ${(props) => (props.wrapperWidth ? props.wrapperWidth : '100%')};
`
const Collapse = styled(Box)`
  position: absolute;
  top: 39px;
  width: 97%;
  z-index: 999;
  display: ${(props) => (props.isOpened ? 'block' : 'none')};
`
const DropdownOptionWrapper = styled(Box)`
  background-color: #22144a;
  transition: all 0.3s ease-out;
`

const DropdownOption = styled(Text)`
  padding: 10px 0px 10px 15px;
  font-weight: 500;
  color: #ffffff;
  align-items: center;
  z-index: 999;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 15.76px;
  transition: all 0.3s ease-out;
  display: flex;
  align-items: center;
  &:hover {
    cursor: pointer;
    background-color: #4c3886;
  }
`
// clip-path: polygon(8.4px 0, 100% 0, 100% 17px, calc(100% - 8.5px) 100%, 0.3% 100%);
const DropdownButton = styled(Flex)`
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 39px;
  clip-path: ${(props) =>
    props.position === 'left'
      ? 'polygon(9px 0, 100% 0, 100% 19px, calc(100% - 9px) 100%, 0% 100%)'
      : 'polygon(9px 0, 100% 0, calc(100% - 9px) 100%, 0% 100%, 0% 19px)'};
  transition: all 0.3s ease-out;
  opacity: ${(props) => (props.disabled ? '0.6' : '1')};
  background: linear-gradient(180deg, #b596dc 0%, #3d296a 100%);
  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 2px;
    height: calc(100% - 4px);
    width: calc(100% - 4px);
    clip-path: ${(props) =>
      props.position === 'left'
        ? 'polygon(8.4px 0, 100% 0, 100% 17px, calc(100% - 8.5px) 100%, 0.3% 100%)'
        : 'polygon(8.4px 0, 99.6% 0, calc(100% - 9px) 100%, 0 100%, 0 17.4px)'};
    background: #4c3886;
  }

  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
  }

  &:active {
    opacity: ${(props) => (props.disabled ? '0.6' : '1')};
  }
`
const OptionText = styled(Text)`
  z-index: 999;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 15.76px;
  font-weight: 500;
  color: #ffffff;
`
const IconWrapper = styled(Box)`
  z-index: 999;
`
const TokenImage = styled.img`
  width: 20px;
  height: 20px;
  z-index: 999;
  margin-right: 5px;
`

const DropdownIcon = styled.img`
  transform: ${(props) => (props.isOpen ? 'rotate(180deg)' : 'rotate(0deg)')};
  transition: all 0.3s ease-out;
  margin-left: 5px;
  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
  }
`

const Dropdown = ({
  value,
  options = [],
  wrapperWidth = '100%',
  onHandleChange = () => {},
  disabled = false,
  position = 'right',
}) => {
  const [isOpen, setIsOpen] = useState(false)
  const optionsRef = useRef(null)
  useClickOutside(optionsRef, () => {
    setIsOpen(false)
  })

  const onDismissDropdown = () => {
    setIsOpen(false)
  }

  return (
    <PriceWrapper wrapperWidth={wrapperWidth} ref={optionsRef}>
      <DropdownButton
        position={position}
        disabled={disabled}
        onClick={() => {
          if (!disabled) {
            setIsOpen(!isOpen)
          }
        }}
      >
        {value && value.img && <TokenImage src={value.img} />}
        <OptionText fontSize="12px">{value && value.symbol}</OptionText>
        <IconWrapper>
          <DropdownIcon src="/images/dropdown-icon.png" isOpen={isOpen} disabled={disabled} />
        </IconWrapper>
      </DropdownButton>
      <Collapse isOpened={isOpen}>
        <DropdownOptionWrapper>
          {options.map((option) => {
            return (
              <DropdownOption
                key={option.label}
                onClick={() => {
                  onHandleChange(option)
                  onDismissDropdown()
                }}
              >
                {option.img && <TokenImage src={option.img} />}
                <Text>{option.symbol}</Text>
              </DropdownOption>
            )
          })}
        </DropdownOptionWrapper>
      </Collapse>
    </PriceWrapper>
  )
}

export default Dropdown
