import { useWeb3React } from '@web3-react/core'
import { ZERO_ADDRESS } from 'config/constant'
import { useCallback } from 'react'
import { buyNft } from 'utils/callHelpers'
import { useCyblocMarketContract } from './useContract'

const useBuyNft = (marketContractAddress, cyblocCoreAddress) => {
  const cyblocMarketContract = useCyblocMarketContract(marketContractAddress)
  const { account } = useWeb3React()
  const handleBuyNft = useCallback(
    async (tokenId, amount, payToken) => {
      const isNativeToken = payToken === ZERO_ADDRESS
      const tx = await buyNft(
        cyblocMarketContract,
        cyblocCoreAddress,
        tokenId,
        payToken,
        account,
        amount,
        isNativeToken,
      )

      return tx
    },
    [cyblocMarketContract, account],
  )

  return { onBuyNft: handleBuyNft }
}

export default useBuyNft
