import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import useWeb3 from 'hooks/useWeb3'
import { approveContract } from 'utils/callHelpers'

export const useApprove = (paytokenContract, marketContractAddress) => {
  const { account } = useWeb3React()

  const handleApprove = useCallback(async () => {
    try {
      const tx = await approveContract(paytokenContract, marketContractAddress, account)
      return tx
    } catch (e) {
      return false
    }
  }, [account, paytokenContract, marketContractAddress])

  return { onApprove: handleApprove }
}
