import React, { useState, useMemo, useCallback } from 'react'
import Box from 'components/Box'
import Text from 'components/Text'
import Flex from 'components/Flex'
import styled from 'styled-components'

import MultiRangeSlider from './MultiRangeSlider'

const StyleText = styled(Text)`
  font-style: normal;
  font-weight: bold;
  font-size: 1.6em;
  line-height: 150%;
  /* identical to box height, or 24px */
  letter-spacing: 0.2em;
  color: #e0e0e0;
`
const StyleTextValue = styled(StyleText)`
  color: #aba7b0;
`
const Wrapper = styled(Box)`
  width: 100%;
  height: 59px;
  z-index: 999;
`
const Header = styled(Flex)`
  justify-content: space-between;
  width: 100%;
  height: 24px;
  top: 7.5px;
`

const Slider = ({ title = '', rangeValue = { min: 0, max: 100 }, handleOnChange }) => {
  // const onChange = useCallback((newValue) => {
  //   console.log(newValue, 'new Value?')
  //   handleOnChange(newValue)
  // }, [newValue])
  const { min, max } = rangeValue
  const onChange = useCallback((newVal) => {
    handleOnChange(newVal)
  }, [])

  return (
    <Wrapper>
      <Header>
        <StyleText>{title}</StyleText>
        <StyleTextValue>
          {min} - {max}{' '}
        </StyleTextValue>
      </Header>
      <MultiRangeSlider limit_range={{ min: 0, max: 100 }} value_range={{ min, max }} onChange={onChange} />
    </Wrapper>
  )
}
export default Slider
