import React, { useState, useEffect, Suspense, useMemo } from 'react'
import Loader from 'react-loader-spinner'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import get from 'lodash/get'
import find from 'lodash/find'
import { Link } from 'react-router-dom'
import Text from 'components/Text'
import Flex from 'components/Flex'
import SuspenseImg from 'components/SuspenseImg'
import { selectPayTokenConfig } from 'state/reducers/app'
import { formatCurrency } from 'utils/formatCurrency'
import {
  Root,
  Card,
  InfoContainer,
  BuyButton,
  PriceWrapper,
  PriceText,
  TokenIcon,
  CyblocImage,
  LoaderBox,
} from './styles'

const BaseCard = ({ item, loading = false }) => {
  const price = get(item, 'priceIndex', 0)
  const itemPayToken = get(item, 'payTokenAddress', null)
  const { payTokens } = useSelector(selectPayTokenConfig)
  const foundedPayToken = useMemo(() => {
    return find(payTokens, (i) => {
      return i.address === itemPayToken
    })
  }, [item, payTokens])

  const renderContent = () => {
    return (
      <>
        <Suspense
          fallback={
            <LoaderBox>
              <Loader type="Oval" color="#007BFF" height={32} width={32} />
            </LoaderBox>
          }
        >
          {/* <CyblocImage src = {item.image}/> */}
          <SuspenseImg
            src={item.image}
            loading="lazy"
            alt="image"
            color="white"
            style={{ width: '100%', height: '100%' }}
            background="images/CyblocPlaceholder.png"
          />
        </Suspense>
        <InfoContainer>
          <BuyButton>Buy</BuyButton>
          <PriceWrapper mt="2px">
            <PriceText>Price</PriceText>
            <Flex>
              <TokenIcon mr="10px" src={get(foundedPayToken, 'img', '')} />
              <PriceText ml="4px">{formatCurrency(price)}</PriceText>
              <PriceText ml="4px">{get(foundedPayToken, 'symbol', '')}</PriceText>
            </Flex>
          </PriceWrapper>
        </InfoContainer>
      </>
    )
  }

  return (
    <Root>
      <Card>
        {loading ? (
          <>
            <CyblocImage src="images/CyblocPlaceholder.png" />
          </>
        ) : (
          <>{renderContent()}</>
        )}
      </Card>
    </Root>
  )
}

export default React.memo(BaseCard)
