import { useCallback } from 'react'
import { getAuction, getCurrentPrice } from 'utils/callHelpers'
import { useCyblocMarketContract } from './useContract'

const useGetAuction = (marketContractAddress, cyblocCoreAddress) => {
  const cyblocMarketContract = useCyblocMarketContract(marketContractAddress)
  const handleGetCurrentAuction = useCallback(
    async (tokenId) => {
      const tx = await getAuction(cyblocMarketContract, cyblocCoreAddress, tokenId)

      return tx
    },
    [cyblocMarketContract],
  )

  const handleGetCurrentPrice = useCallback(
    async (tokenId) => {
      const tx = await getCurrentPrice(cyblocMarketContract, cyblocCoreAddress, tokenId)

      return tx
    },
    [cyblocMarketContract],
  )

  return { onGetCurrentAuction: handleGetCurrentAuction, onGetCurrentPrice: handleGetCurrentPrice }
}

export default useGetAuction
