import React from 'react'
import Flex from 'components/Flex'
import styled from 'styled-components'

const BGRight = styled(Flex)`
  justify-content: center;
  align-items: center;
  position: relative;
  height: 39px;
  transition: all 0.3s ease-out;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  clip-path: polygon(9px 0, 100% 0, 100% 19px, calc(100% - 9px) 100%, 0% 100%);
  background: linear-gradient(180deg, #b596dc 0%, #3d296a 100%);
  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 2px;
    height: calc(100% - 4px);
    width: calc(100% - 4px);
    clip-path: polygon(8.4px 0, 100% 0, 100% 17px, calc(100% - 8.5px) 100%, 0.3% 100%);
    background: #4c3886;
  }

  &:hover {
    opacity: 1;
    cursor: pointer;
  }

  &:active {
    opacity: 1;
  }
`

const BGCenter = styled(Flex)`
  justify-content: center;
  align-items: center;
  position: relative;
  height: 39px;
  transition: all 0.3s ease-out;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  clip-path: polygon(9px 0, 100% 0, calc(100% - 9px) 100%, 0% 100%);
  background: linear-gradient(180deg, #b596dc 0%, #3d296a 100%);
  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 2px;
    height: calc(100% - 4px);
    width: calc(100% - 4px);
    clip-path: polygon(8.4px 0, 99.6% 0, calc(100% - 9px) 100%, 0.3% 100%);
    background: #4c3886;
  }

  &:hover {
    opacity: 1;
    cursor: pointer;
  }

  &:active {
    opacity: 1;
  }
`

const BGLeft = styled(Flex)`
  justify-content: center;
  align-items: center;
  position: relative;
  height: 39px;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  clip-path: polygon(9px 0, 100% 0, calc(100% - 9px) 100%, 0% 100%, 0% 19px);
  transition: all 0.3s ease-out;
  background: linear-gradient(180deg, #b596dc 0%, #3d296a 100%);
  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 2px;
    height: calc(100% - 4px);
    width: calc(100% - 4px);
    clip-path: polygon(8.4px 0, 99.6% 0, calc(100% - 9px) 100%, 0 100%, 0 17.4px);
    background: #4c3886;
  }

  &:hover {
    opacity: 1;
    cursor: pointer;
  }

  &:active {
    opacity: 1;
  }
`

export const TAB_BUTTON_TYPE = {
  left: 1,
  center: 2,
  right: 3,
}

const TabButton = ({ mr = '0px', mt = '0px', type = TabButton.left, isActive = false, onClick, children }) => {
  switch (type) {
    case TAB_BUTTON_TYPE.left:
      return (
        <BGLeft mr={mr} mt={mt} isActive={isActive} onClick={onClick}>
          {children}
        </BGLeft>
      )
    case TAB_BUTTON_TYPE.right:
      return (
        <BGRight isActive={isActive} onClick={onClick}>
          {children}
        </BGRight>
      )

    default:
      return (
        <BGCenter isActive={isActive} onClick={onClick}>
          {children}
        </BGCenter>
      )
  }
}
export default TabButton
