import axios from 'axios'
import { API_MARKETPLACE, NETWORK } from 'config/constant'

// eslint-disable-next-line no-undef

export const useApi = () => {
  const getTokensNftDetail = async (nftId) => {
    try {
      const res = await axios.get(`${API_MARKETPLACE}/market/cybloc/${nftId}`, {
        headers: {
          'x-functions-key': process.env.REACT_APP_AZURE_CODE,
        },
        params: {
          network: NETWORK,
        },
      })
      return res.data
    } catch (error) {
      return null
    }
  }
  const getTokensNftEvents = async (nftId, pageNum, pageSize) => {
    try {
      const res = await axios.get(`${API_MARKETPLACE}/market/cybloc/${nftId}/events`, {
        headers: {
          'x-functions-key': process.env.REACT_APP_AZURE_CODE,
        },
        params: {
          network: NETWORK,
          pageNum,
          pageSize,
        },
      })
      return res.data
    } catch (error) {
      return null
    }
  }

  return {
    getTokensNftDetail,
    getTokensNftEvents,
  }
}
