import { useEffect } from 'react'
import styled from 'styled-components'
import Box from 'components/Box'

const Wrapper = styled(Box)`
  position: relative;
`

const mapType = {
  1: 'Bronze',
  2: 'Silver',
  3: 'Gold',
  4: 'Platinum',
  5: 'Legendary',
}

const CyBlocCard = ({ data }) => {
  const id = data.tokenId || data.cyblocId

  useEffect(() => {
    if (window.spine) {
      const spine = new window.spine.SpinePlayer(`player-container-${id}`, {
        jsonUrl: `/animations/${mapType[data.type]}_Character.json`,
        atlasUrl: `/animations/${mapType[data.type]}_Character.atlas.txt`,
        alpha: true,
        backgroundColor: '#00000000',
        animation: 'Idle',
        defaultMix: 0.1,
        showControls: false,
        success: (player) => {
          const { skeleton } = player
          const keys = Object.keys(data.body)
          keys.forEach((k) => {
            let v = data.body[k]
            v = v >= 10 ? v : `0${v}`
            const skin = skeleton.data.findSkin(`${k}/${k}_${v}`)
            if (skin) {
              skeleton.skin.addSkin(skin)
            }
          })
        },
      })
    }
  }, [])

  return (
    <Wrapper>
      {data.type === 1 || data.type === 2 ? (
        <img
          src={`/Backgrounds/${mapType[data.type]}/Backgrounds/${data.body.card_background}.png`}
          alt="bg-cybloc"
          style={{
            width: '100%',
          }}
        />
      ) : (
        <video width="100%" height="100%" autoPlay muted loop playsInline>
          <source
            src={`/Backgrounds/${mapType[data.type]}/Backgrounds/${data.body.card_background}.mp4`}
            type="video/mp4"
          />
        </video>
      )}
      <div
        id={`player-container-${id}`}
        style={{
          width: '100%',
          height: '100%',
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
        }}
      />
      <video
        width="100%"
        height="100%"
        style={{
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
        }}
        autoPlay
        muted
        loop
        playsInline
      >
        <source
          src={`/Backgrounds/${mapType[data.type]}/Foregrounds/${data.body.card_background}.webm`}
          type="video/webm"
        />
      </video>
      <div
        style={{
          position: 'absolute',
          top: '4.5%',
          left: '25%',
          color: '#ffffff',
          zIndex: 1,
          fontSize: 15,
          textShadow: '0 0 5px rgb(0, 0, 0, 0.5)',
          fontFamily: "'Audiowide', cursive",
          letterSpacing: 0,
        }}
      >
        NO.{id}
      </div>
    </Wrapper>
  )
}

export default CyBlocCard
