import React, { useState, useContext, useCallback, useMemo, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { useWeb3React } from '@web3-react/core'
import Flex from 'components/Flex'
import CheckBox from 'components/CheckBox'
import Text from 'components/Text'
import Box from 'components/Box'

import { Collapse } from 'react-collapse'
import get from 'lodash/get'
import styled from 'styled-components'
import { selectFilters, setFilters } from 'pages/Marketplace/store/reducer'
import SearchInput from 'components/SearchInput'
import { MarketplaceContext } from '../../context/MarketplaceContext'

import DropDownFilter from '../DropDownFilter'

const Content = styled(Box)`
  position: relative;
  padding: 45px 24px 24px 24px;

  flex-wrap: wrap;
  align-items: center;
  width: 100%;
`
const Label = styled.label`
  // margin-top: 3px;
  margin: 4px;
  font-style: normal;
  font-weight: bold;
  font-size: 1.3em;

  color: #e0e0e0;
`

const SeacchCybloc = ({ setQuery, query }) => {
  const { state, marketplaceDispatch } = useContext(MarketplaceContext)
  const filters = useSelector(() => selectFilters(state))
  const [searchId, setSearchId] = useState('')
  const { account } = useWeb3React()
  const { cyblocId } = query
  const [isOpen, setIsOpen] = useState(true)

  useEffect(() => {
    if (cyblocId) {
      setSearchId(cyblocId)
    } else {
      setSearchId('')
    }
  }, [cyblocId])

  const onHandleClickSearch = () => {
    // Change filter info here
    marketplaceDispatch(
      setFilters({
        ...filters,
        id: searchId,
      }),
    )
    setQuery({
      cyblocId: searchId,
      pageNumber: 1,
    })
  }

  const onChange = (e) => {
    const newVal = get(e, 'target.value', null)
    setSearchId(newVal)
  }

  const onKeyDown = (e) => {
    // User pressed the enter key
    if (e.keyCode === 13) {
      onHandleClickSearch()
    }
  }
  const onChangeState = () => {
    setIsOpen(!isOpen)
  }
  const handleCheckboxChange = (value) => {
    // Change filter info here
    marketplaceDispatch(
      setFilters({
        ...filters,
        showMyAuction: value,
      }),
    )
    setQuery({
      showMyAuction: value,
      pageNumber: 1,
    })
  }
  return (
    <DropDownFilter isOpen={isOpen} title="Search" onChangeState={onChangeState}>
      <Collapse isOpened={isOpen}>
        <Content>
          <SearchInput
            onHandleClickSearch={onHandleClickSearch}
            onChange={onChange}
            type="number"
            placeholder="CyBloc id"
            onKeyDown={onKeyDown}
            value={searchId}
            style={{
              maxWidth: 'none',
              flex: 1,
              // height: '39px'
            }}
          />
          {account ? (
            <>
              <Flex m="20px 24px 0px 24px">
                <CheckBox
                  // label="Show my auction"
                  id="my-auction"
                  value={filters.showMyAuction}
                  checked={filters.showMyAuction}
                  onChange={() => handleCheckboxChange(!filters.showMyAuction)}
                />
                <Label htmlFor="my-auction">Show My Listings</Label>
              </Flex>
            </>
          ) : (
            <></>
          )}
        </Content>
      </Collapse>
    </DropDownFilter>
  )
}
export default SeacchCybloc
